import React, { useState } from 'react';
 
import { Form, Row, Col, Input, Button,AutoComplete,notification } from 'antd';
//import DisplayTBForm from "./DisplayTBForm";
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import "./TBForm.css";

function TBForm(props){ 

  // props.user.obj.username
  const [form] = Form.useForm(); 

  const openNotification = () => {
    notification['success']({
      message: 'Successfully Add New Parcel',
      description:
        'You have successfully add an new Parcel, you can not manipulate it until it become verify',
      })
  };


  const onFinish = values => {
    const postInfo = {
                        orderNumber:values.company+" "+values.courier, // 成功添加空格
                        senderName:values.senderName,
                        senderAddress:values.senderAddress,
                        senderPhone:values.senderPhone,
                        senderPostCode:values.senderPostCode,
                        userId:props.user.obj.uid,
                        receiverName:"",   // 这几条必须传递 否则后台数据库就出错了 后台可否改为非强制传递？ 即写作null也可以
                        receiverAddress:"",
                        receiverPhone:"",
                        receiverPostCode:"",
                        contentType:values.contentType,
                        description:values.description,
                        //  "userOrderId": -1 不需要输入 自动返回-1 不懂这个所属的意思 4 是什么 为什么返回就是-1 了代表现在不属于任何包裹
                    }

    const returnInfo = JSON.parse(JSON.stringify(postInfo));
          returnInfo.userOrderId = -1;
          returnInfo.parcelStatus = "waiting";
          
       
    const newRecord = props.record.concat(returnInfo)  //
                  
    console.log("stringfy new TB record newnew:")
    console.log(JSON.stringify(postInfo))
 
    fetch("/api/parcel/create", { 
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(postInfo),
    })
      .then(
        openNotification(),
        form.resetFields()
        )  
      .then(
        console.log("newRecord in set Record"),
        console.log(newRecord),
        props.setRecord(newRecord)); ;  // 成功添加完毕后才能清空 并且添加新卡信息 否则不会触发  另外单独点击clear也有一样的效果 但是是再写错想要清除全部的情况下进行
  };

  // 这里点击触发onFinish 事件

  const renderTitle = title => (
    <span>
      {title}
    </span>
  );
  
  const renderItem = (title) => ({
    value: title,
    label: (
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {title}
      </div>
    ),
  });

  const options_type = [
    {
      label: renderTitle("货物类型"),
      options: [renderItem("普货"),renderItem("文件"),renderItem("电子产品"),renderItem("化妆品"),renderItem("食品"),renderItem("其他")],
    }
  ];
  
  const options_company  =[{value:"SF顺丰"},{value:"EMS"},{value:"BS百世"},{value:"ZT中通"},{value:"ST申通"},{value:"YT圆通"},{value:"YD韵达"},{value:"TT天天"},{value:"ZJS宅急送"},{value:"DB德邦"},]
  
    return (
      <Form
        form={form}
        name="InputTBCard"
        className="ant-advanced-search-form"
        onFinish={onFinish}
      >
        <Row gutter={24}>

        <Col span={6} key={1}>
            <Form.Item
              name="company"
              label="快递公司"
              rules={[
                {
                  required: true,
                  message: '请选择公司或输入',
                },
              ]}
            >
            <AutoComplete
              style={{
                //width: 200,
              }}
              options={options_company}
              placeholder="输入快递公司"
              filterOption={(inputValue, option) =>
                option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
              }>
              <Input />  
              </AutoComplete>
            
            </Form.Item>
          </Col>

          <Col span={6} key={2}>
            
            <Form.Item
              name="courier"
              label="快递单号"
              rules={[
                {
                  required: true,
                  message: '请输入快递单号',
                },
              ]}
            >
              <Input />
            </Form.Item>
           
          </Col>

          <Col span={6} key={3}>
            <Form.Item
              name="contentType"
              label="货物类型"
              rules={[
                {
                  required: true,
                  message: '请选择货物类型',
                },
              ]}
            >
              <AutoComplete
                dropdownClassName="certain-category-search-dropdown"
                //dropdownMatchSelectWidth={200}
                style={{
                  //width: 250,
                }}
                options={options_type}
              >
                <Input.Search placeholder="选择类型" />   
              </AutoComplete>
            </Form.Item>
          </Col>
  
          <Col span={6} key={4}>
            <Form.Item
              name="description"
              label="内容描述"
              rules={[
                {
                  required: true,
                  message: '请填写包裹内容',
                },
              ]}
            >
              <Input placeholder="eg: 一件衣服" />
            </Form.Item>
          </Col>
        </Row>

        <Row gutter={24}>

          <Col span={6} key={5}>
              <Form.Item
                name="senderName"
                label="* 寄件人名"
                rules={[
                  {
                    required: false,
                    message: '请输入寄件人姓名',
                  },
                ]}
              >
                <Input placeholder="寄件人或淘宝店名"/>
              </Form.Item>
            </Col>

            <Col span={6} key={6}>
              
              <Form.Item
                name="senderAddress"
                label="* 寄件地址"
                rules={[
                  {
                    required: false,
                    message: '包裹发出的地址',
                  },
                ]}
              >
                <Input placeholder="包裹发出的地址" />
              </Form.Item>
            
            </Col>

            <Col span={6} key={7}>
              <Form.Item
                name="senderPostCode"
                label="* 寄件邮编"
                rules={[
                  {
                    required: false,
                    message: '请输入邮编',
                  },
                ]}
              >
                  <Input />   
             
              </Form.Item>
            </Col>

            <Col span={6} key={8}>
              <Form.Item
                name="senderPhone"
                label="* 联系方式"
                rules={[
                  {
                    required: false,
                    message: '请填写寄件人号码',
                  },
                ]}
              >
                <Input placeholder="寄件人电话或邮箱"/>
              </Form.Item>
            </Col>
          </Row>

        <Row>
          <Col
            span={24}
            key={9}
            push={10}
            style={{
              textAlign: 'center',
            }}
          >
            <Button type="primary" htmlType="submit" >   
              Add
            </Button>
            <Button
              style={{
                margin: '0 8px',
              }}
              onClick={() => {
                form.resetFields();
              }}
            >
              Clear
            </Button>
          </Col>
        </Row>
      </Form>
    )}

  export default  TBForm;

  
