import React,{useState, useEffect}from "react";
import { Transfer, Table, Tag,Button,notification } from 'antd';
import { Link } from 'react-router-dom';
import _get from "lodash/get";
import difference from 'lodash/difference';
 
import "./AddParcelToOrder.css";

// fitter 过滤合集
//https://www.itranslater.com/qa/details/2136426396867101696
const openNotification = () => {
  notification['success']({
    message: 'Successfully update user information',
    description:
      'You have successfully unpdate your personnal information',
    })
  };

const TableTransfer = ({ leftColumns, rightColumns, ...restProps }) => (
  <Transfer {...restProps} showSelectAll={false}>
    {({
      direction,
      filteredItems,
      onItemSelectAll,
      onItemSelect,
      selectedKeys: listSelectedKeys,
      disabled: listDisabled,
    }) => {
      const columns = direction === 'left' ? leftColumns : rightColumns;

      const rowSelection = {
        getCheckboxProps: item => ({ disabled: listDisabled || item.disabled }),
        onSelectAll(selected, selectedRows) {
          const treeSelectedKeys = selectedRows
            .filter(item => !item.disabled)
            .map(({ key }) => key);
          const diffKeys = selected
            ? difference(treeSelectedKeys, listSelectedKeys)
            : difference(listSelectedKeys, treeSelectedKeys);
          onItemSelectAll(diffKeys, selected);
        },
        onSelect({ key }, selected) {
          onItemSelect(key, selected);
        },
        selectedRowKeys: listSelectedKeys,
      };

      return (
        <Table
          rowSelection={rowSelection}
          columns={columns}
          dataSource={filteredItems}
          size="small"
          style={{ pointerEvents: listDisabled ? 'none' : null }}
          onRow={({ key, disabled: itemDisabled }) => ({
            onClick: () => {
              if (itemDisabled || listDisabled) return;
              onItemSelect(key, !listSelectedKeys.includes(key));
            },
          })}
        />
      );
    }}
  </Transfer>
);

const leftTableColumns = [
  {
    dataIndex: 'TBindex',
    title: '序号', // 这就是自动分配的序号 但是所有的key 才是唯一用来处理的关键词 其他都不参考
  },
  {
    dataIndex: 'title',
    title: '淘宝单号',
  },
  {
    dataIndex: 'tag',
    title: '包裹内容',
    render: tag => <Tag>{tag}</Tag>,
  },
  {
    dataIndex: 'description',
    title: '描述',
  },
  {
    dataIndex:"status",
    title:"状态",
    render: tag => <Tag>{tag}</Tag>,
  }
];
const rightTableColumns = [
  {
    dataIndex: 'TBindex',
    title: '序号',
  },
  {
    dataIndex: 'title',
    title: '淘宝单号',
  },
];


export default function AddParcelToOrder(props) {

  const [dataSource,setDataSource] = useState([])    //  某一用户名字下所有包裹 （包括有分配订单和没有分配订单的包裹）

  const userId = props.user.obj.uid.toString();

  const thisOrderId = props.match.params.id;   // 当前进入的包裹id

  useEffect(()=>{
    fetch("/api/parcel/findAll/"+ thisOrderId, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // 拿到的是dictionary格式 Json ：[{"key":"123"}] 转换为obj 从而进行操作；
    .then(data => {
      const originTargetKeys = data.map(x=>x.id)
      setState({ targetKeys:  originTargetKeys })
     })

      fetch("/api/parcel/findAll/userid/"+ userId, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())   
      .then(data => {
        setDataSource(data);  
       }) 
  },[])



const mockData = dataSource.filter(
  item => item.userOrderId == thisOrderId || item.userOrderId == -1   // orderId 状态为 2 即已经在本包裹里了 可以进来放在右边 或者 无包裹状态为 -1 可以放在左边挑选
  )
  .map(
    (item,index)=>{return {
      TBindex:index,  // 自动给的顺序码
      key:item.id,   // parcel id; 数据库中包裹的指定唯一ID
      title: item.orderNumber,
      description: item.description,
      disabled: item.parcelStatus !== "verify", // 打印出来看看 =》 问题包裹不可添加
      tag:item.contentType,
      belongToOrder:item.userOrderId,  // 属于 当前order 或者 -1
      status: item.parcelStatus,}     // verify/waiting/problem 显示出来会更直观些
      }
    )
      
const [state, setState] = useState({
  targetKeys: [], //originTargetKeys,  首先将会在use Effect中去拿到 然后点击onclick后会出发set 然后改变targetkey值 但是由于useEffect只可以执行一次就停止 所以不会对后续渲染产生问题
  disabled: false, // 因为我这里不需要禁用按钮 所以可以移除 或者写做 false
  showSearch: true, // 可以一直保留搜索框 写true
  })

const onChange = nextTargetKeys => { //Advice: rename this funciton. 
  setState({ targetKeys: nextTargetKeys });}   // targetKeys 会根据onChange一直变化的 接下来是要拿到跟随nextTargetKeys拿到的需要更新的数据
 

const mockList = mockData.map(item => item.key)  // 本次过滤中所有的的包裹key列表 [8, 11, 12, 13, 14, 15, 16, 18]


const LeftParcelList = mockList.filter(
    function(e) {
      return this.indexOf(e) < 0;
      },
    state.targetKeys //  只要 targetKeys 改变 LeftParcelList 就会改变
  );


const targetParcel = mockData.filter(item=>state.targetKeys.includes(item.key))  // 在提交表单时候 目标包裹中所有需要更新的信息

const  LeftParcel  = mockData.filter(item=>LeftParcelList.includes(item.key))  // 在提交表单时候 左边包裹中所有需要order所属为-1的信息 

const targetUpdate = targetParcel.filter(item =>item.belongToOrder == -1).map(item=>item.key)    //找出目标包裹中现在是-1但是要改为2的包裹 这些都是要从-1 移动到2的包裹

const LeftUpdate = LeftParcel.filter(item =>item.belongToOrder !== -1).map(item=>item.key)  // 找到左边所有需要移除的包裹 它们的originOrder是当前包裹号码


// 每次点击onchange  就会对该订单下的包裹做一个整体评价 去看是否需要修改内容然后提交

// 以下两个Update是用来提交数据 发送请求改变包裹位置的 

const submitOrder =()=>{
  Promise.all(LeftUpdate.map(item=>{
        fetch(`/api/parcel/moveout/parcel/${item}/userorder/${thisOrderId}`, // 移入包裹
          {  
            method: "PUT",
            headers: {
              "Content-Type": "application/json"
            },
          }); 
        }))
  .then(fetch(
    `/api/parcel/move?newOrderId=${thisOrderId}&orginOrderId=-1`,  // 移除包裹
          {
            method: "PUT",
            headers: {
              "Content-Type": "application/json"
            },
            body: JSON.stringify(targetUpdate),
          }))
  .then(openNotification())
  }


    return (
      <div>
        <TableTransfer
          dataSource={mockData}
          titles={['可添加的包裹', '订单'+thisOrderId+'内含包裹']}
          targetKeys={state.targetKeys}
          disabled={state.disabled}
          showSearch={state.showSearch}
          onChange={onChange}
          filterOption={(inputValue, item) =>
            item.title.indexOf(inputValue) !== -1 || item.tag.indexOf(inputValue) !== -1
          }
          leftColumns={leftTableColumns}
          rightColumns={rightTableColumns}
        /> 
        <div className="parcelUpdate">
          <Link to="/Business/OrderHome">
            <Button size="large" style={{ float: 'center', margin: 5 }}>
              返回
            </Button>
          </Link>
          <Button id="submitOrder" size="large" style={{ float: 'center', margin: 5}} onClick={submitOrder}> 
            保存
          </Button> 
        </div>
      </div>
    );
}
