import React,{useState,useEffect} from 'react';
import {Table,Tag,notification,Rate,Modal } from "antd";
import {EditOutlined,ThunderboltFilled,HeartFilled} from '@ant-design/icons'; // <ThunderboltFilled />
import { Link } from "react-router-dom";
import { Drawer, Form, Button, Col, Row, Input, Select, DatePicker } from 'antd';
import "./NoCommit.css";


export default function NoCommit(props) {

  // record 数据获取
    const userId = props.userId // props.user.obj.uid.toString();
    const userName = props.userName //props.user.obj.username.toString();

  // 关于抽屉的基础状态设置

    const FormItem = Form.Item;

    const [form] = Form.useForm();

    const [drawer,setDrawer] = useState({ visible: false, drawerOId:"",drawerUId:userId}) 

    const  showDrawer = (prop) => {
        setDrawer({
        visible: true,
        drawerOId: prop, // prop,  // record.id 被传递进去了
        drawerUId:userId,// userId 也传递进去 用来后面提交触发函数
        });
    };

    const onClose = () => {
        setDrawer({
        visible: false,
        drawerOId:"",
        drawerUId:userId,
        });
    };

    const handleSubmit = (values)=>{

      const SumScore = values.star + values.heart

      const CreateComment =
      {
        userId: drawer.drawerUId,
        userOrderId:drawer.drawerOId,
        score: SumScore,
        comment:values.description,
      }
    

    fetch("/api/orderhistory/create", { 
      method: "POST",
      headers: {
      "Content-Type": "application/json"
      },
      body: JSON.stringify(CreateComment),
      })
      .then(resp => resp.json())
      .then(showModal())  // 点击提交时候弹出对话框
    }

// 对话框弹出

const [modalState,setModalState] = useState({visible: false}) 

const showModal = () => {
  setModalState({
    visible: true,
  });
};

const handleOk = e => {
  //console.log(e);
  setModalState({
    visible: false,
  });
  // 同时关闭抽屉
  setDrawer({
    visible: false,
    drawerOId:"",
    drawerUId:userId,
    })
};

const handleCancel = e => {
  //console.log(e);
  setModalState({
    visible: false,
  });
};


const columns = (showDrawer)=>{
  return [
  {
    title: "订单号",
    dataIndex: "id",
    key: "id",
  },
  {
    title: '订单状态',
    key: 'statusId',
    dataIndex: 'statusId',
    },
  {
    title: "去评价",  // 这里只需要拿到  record.id 并去跳转就可以了
    dataIndex: "action",
    key: "action",
    render: (text, record, index) => { 
      return (
        <div>
            <Link to={`/Business/History/Commit/${record.id}`} className="More"> 
            <EditOutlined/>
            </Link>
        </div>
      );}
  },
  {
    title: "打开评价抽屉",  // 这里只需要拿到  record.id 并去跳转就可以了
    dataIndex: "action",
    key: "action",
    render: (text, record, index) => { 
      return (
        <Button type="primary" onClick={()=>showDrawer(record.id)}>  
            打开
        </Button>
      );}
  },
];}
//showDrawer(record.id)    /// onclick 事件里面的函数如何获得参数？ 想要拿到record id 然后去抽屉里面进行提交//https://blog.csdn.net/hl_qianduan/article/details/89372881?utm_medium=distribute.pc_relevant.none-task-blog-BlogCommendFromMachineLearnPai2-2.channel_param&depth_1-utm_source=distribute.pc_relevant.none-task-blog-BlogCommendFromMachineLearnPai2-2.channel_param

let tableDataSource = [];

if(props.recordNeedCmt){
  tableDataSource = props.recordNeedCmt.map(item => {
    return {
      ...item,
      key: item.id
    }
  })
}

return(

  <div>
  <p>已完成的订单</p>
  <br/>
  <Table 
  dataSource={tableDataSource}
  columns={columns(showDrawer)}   // 要在这里绑定触发函数
  expandable={
    {
      expandedRowRender: (record) => {
        return(
        <div>
          <p>收件人 &nbsp; {record.receiverName} &nbsp; 地址  &nbsp; {record.receiverAddress } &nbsp; {record.receiverPostCode} </p>
          <p>重量  &nbsp; {record.weight } &nbsp; 体积  &nbsp; {record.volumn}</p>
          <p>价格  &nbsp; {record.price} 付费信息 &nbsp; {record.paymentInfo}</p>
        </div>
        )}
    }
  }
   />

    <Drawer
          title="请打分"
          width={720}
          onClose={onClose}
          visible={drawer.visible}
          bodyStyle={{ paddingBottom: 80 }}
          footer={
            <div
              style={{
                textAlign: 'right',
              }}
            >
              <Button onClick={onClose} style={{ marginRight: 8 }}>
                取消评价
              </Button>
             
            </div>
          }
    >
      <Form 
      layout="vertical" 
      hideRequiredMark
      form={form}
      onFinish={handleSubmit}>
        <Row>
            <Col span={4}>
            亲爱的{userName} 请评价 {drawer.drawerUId} 的 订单 {drawer.drawerOId} 号
            </Col>
        </Row>
        <Row>
          <Col span={20}>
            <Form.Item
              className="heart"
              name="heart"
              label="一闪服务满意程度"
              rules={[{ required: true, message: 'Please choose the star' }]}
            >
              <Rate allowClear={true} allowHalf style={{ fontSize: 36 }} character={<HeartFilled />} />
            </Form.Item>
          </Col>
        </Row>
        <Row>
          <Col span={20}>
            <Form.Item
              className="flash"
              name="flash"
              label="物流速度满意度"
              rules={[{ required: true, message: 'Please choose the star' }]}
            >
              <Rate allowClear={true} allowHalf style={{ fontSize: 36 }} character={<ThunderboltFilled />} />
            </Form.Item>
          </Col>
        </Row>
        
        <Row gutter={16}>
          <Col span={24}>
            <Form.Item
              name="您想对我们说"
              label="Description"
              rules={[
                {
                  required: true,
                  message: 'please enter url description',
                },
              ]}
            >
              <Input.TextArea rows={4} placeholder="please enter url description" />
            </Form.Item>
          </Col>
        </Row>
        <Row>
            <FormItem>
                <Button type="primary" htmlType="submit"  >  
                    提交
                </Button> 
            </FormItem>
        </Row>
      </Form>
    </Drawer>

    <Modal
      title="评价成功"
      visible={modalState.visible}
      onOk={handleOk}
      onCancel={handleCancel}
    >
      <p>非常感谢您的支持 一闪期待再次为您提供服务</p>
    </Modal>
</div>

)
  }
