import React from "react";
import "./AParcelSearch.css";
import { Form, Row, Col, Input, Button,Select,} from 'antd';
 


 //const ReturnInfo = JSON.parse(JSON.stringify(postInfo)) 一种将obj直接复制存储到另外地址的方法

export default function AParcelSearch(props){ 


  const [form] = Form.useForm(); 
  const { Option } = Select;

    
  const onFinish = values => {
    props.setSearch({userId:values.userId,
                    orderId:values.orderId,
                    username:values.username,
                    searchRg:values.searchRange,
                    searchTy:values.searchType}) 
                    };

    return (
      <div>
        <p className="ParcelSearchIntro"> <span>以下两个搜索框只需要输入一个就可以查询; 每次修改状态后 请点击刷新并重新点击搜索键 新的搜索状态即可显示</span></p>
      <Form
        form={form}
        name="SearchOrder"
        className="Aparcel-search-form"
        onFinish={onFinish}
      >
        <Row gutter={24}>
          <Col span={8} key={1}> 
              <Form.Item
              name="userId"
              label="用户ID"
              > 
            <Input />  
          </Form.Item>
          </Col>

          <Col span={8} key={2}> 
              <Form.Item
              name="username"
              label="用户姓名"
              > 
            <Input />  
          </Form.Item>
          </Col>

          <Col span={8} key={3}> 
                <Form.Item
                name="orderId"
                label="订单ID"
                > 
              <Input />  
            </Form.Item>
            </Col>
        </Row>

        <Row>
          <Col span={12} key={4}> 
            <Form.Item
                className="searchAll"
                name="searchRange"
                label="全部包裹"
                > 
              <Select className="select">
                <Option value="">否</Option>
                <Option value="searchAll">是</Option>
              </Select>
            </Form.Item>
          </Col>
          <Col span={12} key={5}> 
            <Form.Item
              className="searchStatus"
              name="searchType"
              label="包裹状态"
              > 
              <Select className="select" >
                <Option value="">无</Option>
                <Option value="waiting">等待件</Option>
                <Option value="problem">问题件</Option>
                <Option value="verify">认证件</Option>
              </Select>
            </Form.Item>
          </Col>
        </Row>

        <Row>
          <Col
            span={24}
            key={6}
           // push={10}
            style={{
              textAlign: 'center',
              justifyContent:'center'
            }}
          >
            <Button type="primary" htmlType="submit" >   
              搜索
            </Button>
            <Button
              style={{
                margin: '0 8px',
              }}
              onClick={() => {
                //form.resetFields();
                props.setSearch({userId:"",
                    orderId:"",
                    username:"",
                    searchRg:"",
                    searchTy:""}) 
              }}
            >
              刷新
            </Button>
            
            <Button
              style={{
                margin: '0 8px',
              }}
              onClick={() => {
                form.resetFields();
              }}
            >
              清除
            </Button>
          </Col>
        </Row>
      </Form>
      </div>
    )}