import React,{useState,useEffect}from "react";
import _get from "lodash/get";
import {Table, Form, Popover,Button,Input,notification } from "antd";
import {EditOutlined} from '@ant-design/icons';
import AUserInfoForm from "../AUserInfoForm/AUserInfoForm";
import "./AdminUserInfo.css";


export default function AdminUserInfo(props) {

  const [record,setRecord] = useState([])  //props.user.obj.uid
 
  // record 必须写成list才可以放到table之中

  useEffect(()=>{
    fetch("/api/user/findAll", { 
      method: "Get",
      headers: {
        "Content-Type": "application/json"
      },
    })
      .then(resp => resp.json())
      .then(data => {
        setRecord(data);   
      });
  },[record.length]) 

  console.log("长度")
  console.log(record.length)

  let userRecord;

  if(record){
    userRecord = record.filter(item=>{if(item.role === "user")return(item)})
  }


  return (
    <div className="AdminUserInfo">
        <AUserInfoForm user={props.user} record={record} setRecord={setRecord} userRecord={userRecord}/>  
    </div>
  ) 
}