import React from "react";
import { Route } from "react-router-dom";
import AdminTopBar from "../AdminTopBar/AdminTopBar";
import AdminOrder from "../AOrderHome/AdminOrder/AdminOrder";
import AdminParcel from "../AParcelHome/AdminParcel/AdminParcel";
import AOrderUpdate from "../AOrderHome/AOrderUpdate/AOrderUpdate";
import AParcelUpdate from "../AParcelHome/AParcelUpdate/AParcelUpdate";
import AdminUserInfo from "../AUserHome/AdminUserInfo/AdminUserInfo";
import AUserInfoDetail from "../AUserHome/AUserInfoDetail/AUserInfoDetail";
import AdminRule from "../../HomeAdmin/AdminRule/AdminRule";
//import AdminTest from "../AdminTest/AdminTest";
import ATracking from "../AOrderHome/ATracking/ATracking";



export default function AdminDashBoard(props) {

  return (

    <div className="SDashBoard">

        <AdminTopBar user={props.user} />

        <Route
            exact path="/"
            render={internalProps => (
                <AdminRule
                    {...internalProps}
                    user={props.user}
                    isAuthed={true}
                />
            )}
        /> 
      
        <Route
            exact path="/Admin/UserInfo"
            render={internalProps => (
                <AdminUserInfo
                    {...internalProps}
                    user={props.user}
                    isAuthed={true}
                />
            )}
        /> 
        <Route
            exact path="/Admin/UserInfo/:id"
            render={internalProps => (
                <AUserInfoDetail
                    {...internalProps}
                    user={props.user}
                    isAuthed={true}
                />
            )}
        /> 
        <Route
            exact path="/Admin/Order"
            render={internalProps => (
                <AdminOrder
                    {...internalProps}
                    user={props.user}
                    isAuthed={true}
                />
            )}
        /> 

        <Route
        exact path="/Admin/Order/Update/:id"
        render={internalProps => (
          <AOrderUpdate
            {...internalProps}
            user={props.user}
            isAuthed={true}
          />
          )}
          /> 

        <Route
        exact path="/Admin/Parcel"
        render={internalProps => (
          <AdminParcel
            {...internalProps}
            user={props.user}
            isAuthed={true}
          />
          )}
          /> 

        <Route
        exact path="/Admin/Parcel/Update/:id"
        render={internalProps => (
          <AParcelUpdate
            {...internalProps}
            user={props.user}
            isAuthed={true}
          />
          )}
          />

        <Route
            exact path="/Admin/Order/ATrackingInfo/:id"
            render={internalProps => (
            <ATracking
                {...internalProps}
                user={props.user}
                isAuthed={true}
            />
            )}
          /> 
    </div>   
  );
}
