import React, {useState,useEffect}from "react";
import _get from "lodash/get";
// import Masonry from "react-masonry-component";
import 'antd/dist/antd.css'
import { Popover } from 'antd';
import "./Price.css";

export default function Price() {


  const [data, setData]= useState([]);

  useEffect(
    ()=> {
    fetch('https://api.airtable.com/v0/appSpJk1kMrgdZ5rv/Country?api_key=keykDUJvFlbyOqcH8')
    .then((resp) => resp.json())
    .then(data => {
      console.log(data); // Array 5 
      setData(data.records); // records:[{},{},{}]
    }).catch(err => {
      // Error
    })
  }, [0]) 


  return (
    <div>
    <div className="work_title">Below is price info</div>
   
      <div className="grid">
        <div className="grid-sizer" />
        {data.map((item, index) => {
           const objectId = _get(item, "id");
           const dataUrl = _get(item.fields.pictureURL[0], "url");
           const dataInfo = _get(item.fields, "Notes")
           const content =(<p>{dataInfo}</p>)  // 在这里设计好内容显示

           console.log(objectId);
           console.log("图片URL是什么")
           console.log(dataUrl)
           console.log(dataInfo)
          return (
            <div
              className={`grid-item`} // className={`grid-item grid-item--width${index}`} 
              key={item.id}
            >
              <Popover content ={content} title="policy" placement="right" trigger="click">
                <img width="500" src={dataUrl} alt={item.Country} />
              </Popover>   
            </div>
          );
        })}
      </div>
 
  </div>
  );
}
