import React,{useState, useEffect}from "react";
import _get from "lodash/get";
import {Table, Form, Popover, Button,Tag,notification } from "antd";
import {EditOutlined,StopOutlined,PlusSquareOutlined,CloseOutlined,CheckOutlined,PlayCircleOutlined,VerticalAlignTopOutlined} from '@ant-design/icons';
import { Link } from "react-router-dom";
import "./AOrderForm.css";

// antd table columns 内部绑定button的方式 非常重要！
//https://stackoverflow.com/questions/52767825/unable-to-update-the-state-when-button-inside-table-cell-clicked-react-ant-des


/***  提交完成后的提醒框 ** */
const openNotification = () => {
  notification['success']({
    message: '提交成功',
    description:
      '您的订单已处理成功 请点击刷新键重新新搜索其他订单信息'
    })
};

export default function AOrderForm(props) {

  //const userId = props.user.obj.uid;

  /*  order所有6种状态：New=1； submit=2 ; APPROVED = 3; CLOSED = 4; PROCESSING = 5; FINISH = 6; */
  // 确认包裹都收到 同意处理 就 approve，并且添加价格信息； 收到款项以后，就processing， 表示开始处理
 
    /*-----------------按钮绑定函数-------------------------*/

    // 接受 approve  2 => 3

    const approveOrder = (record)=>{
  
      fetch(`/api/order/approve/user/${record.userId}/userorder/${record.id}`, { 
       method: "PUT",
       headers: {
       "Content-Type": "application/json"
       },
       })
       .then(resp => resp.json())
       .then(data => props.setRecord([data])) // 
       .then(openNotification())      // 重新改变record值回到返回值 某条记录 然后由于record变化触发整体重新渲染 到上层use Effect时候 对比发现【record.length】发生了改变 重新渲染全部信息 就拿到了最新的信息
     }

    
    // 关闭 close 2-4 
  const closeOrder = (record)=>{
  
   fetch(`/api/order/close/user/${record.userId}/userorder/${record.id}`, { 
    method: "PUT",
    headers: {
    "Content-Type": "application/json"
    },
    })
    .then(resp => resp.json())
    .then(data => props.setRecord([data])) 
    .then(openNotification())     // 重新改变record值回到空值 然后由于record变化触发整体重新渲染 到上层use Effect时候 对比发现【record.length】发生了改变 重新渲染全部信息 就拿到了最新的信息
  }

  // 处理 3-5
  const processingOrder = (record)=>{
    fetch(`/api/order/processing/user/${record.userId}/userorder/${record.id}`, { 
     method: "PUT",
     headers: {
     "Content-Type": "application/json"
     },
     })
     .then(resp => resp.json())
     .then(data => props.setRecord([data]))
     .then(openNotification())
    }
  
   // 结束 5-6

   const finishOrder = (record)=>{
    fetch(`/api/order/finish/user/${record.userId}/userorder/${record.id}`, { 
     method: "PUT",
     headers: {
     "Content-Type": "application/json"
     },
     })
     .then(resp => resp.json())
     .then(data => props.setRecord([data])) 
     .then(resp => openNotification(resp))
   }

  // --------------- order table 所有columns 的内容----------------------

  const columns = (approveOrder, processingOrder,finishOrder,closeOrder)=>{
    return [
    {
      title: "订单号",
      dataIndex: "id",
      key: "id",
      render: text => <div>{text}</div>,
    },
    {
        title: "用户ID",
        dataIndex: "userId",
        key: "userId",
        render: text => <div>{text}</div>,
      },
    
    {
      title: '订单状态',
      key: 'statusId',
      dataIndex: 'statusId',
      render: (text, record, index) => { 
            let status = ""
            let color = "" 
            if (record.statusId === 1){
              color = "defaut";
              status = "未提交"
            }  
            if (record.statusId === 2){
              color = "warning";
              status = "待处理"
            } else if(record.statusId === 3){
              color = "success";
              status = "已接受"
            } else if(record.statusId === 4){
              color = 'error';
              status = "关闭"
            }
            else if(record.statusId === 5){
              color = "processing";
              status = "正在处理"
            }
            else if(record.statusId === 6){
              color = "defaut";
              status = "已结束"
            }
            return (
              <Tag color={color} key={index}>
                {status}
              </Tag>
            );
          }},

    {
      title: "接受订单", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => { 
        if(record.statusId === 2){return (
          <Button shape="round" type="primary" icon ={<CheckOutlined />} onClick={()=>approveOrder(record)}>     
          </Button>
        );}
        else{return(
          <Button shape="round" icon ={<CheckOutlined />} disabled = {true} />     
        )}
      }
    },
    {
        title: "关闭订单", 
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => { 
          if(record.statusId === 2){return (
            <Button shape="round" type="primary" icon ={<CloseOutlined />} onClick={()=>closeOrder(record)}>     
            </Button>
          );}
          else{return(
            <Button shape="round" icon ={<CloseOutlined />} disabled = {true} />     
          )}
        }
      },

      {
        title: "应收款项", 
        dataIndex: "price",
        key: "price",
        render: (text) => { 
          if(text === null){return (
            <div>暂无</div>
          );}
          else{return(
            <div>RMB: {text}</div>    
          )}
        }
      },
      {
        title: "实收款项", 
        dataIndex: "paymentInfo",
        key: "paymentInfo",
        render: (text) => { 
          if(text === null){return (
            <div>暂无</div>
          );}
          else{return(
            <div>{text}</div>    
          )}
        }
      },

    {
      title: "款项处理/订单修改",  // 这里只需要拿到  record.id 并去跳转就可以了
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  // 这里的record是指传入表中的data 即 datasource ！ 参数只能是这里面的内容 record = props.dataSource 付款状态后面需要修改
        // 如果金额栏目有值 则写应付金额 如果没有 则写 填写支付信息
        if(record.statusId === 3){
          return (
            <Link to={`/Admin/Order/Update/${record.id}`} className="PriceInfo">
            <Button shape="round" type="primary" >     
              添加应收款项
            </Button>
            </Link>
          );
        }
        else if(record.statusId === 5 || record.statusId === 6 ){
          return (
            <Link to={`/Admin/Order/Update/${record.id}`} className="MorePrice">
              <Button shape="round" type="primary" >     
              查看支付信息
              </Button>
            </Link>
          );
        } 
        else{
          return (
            <Link to={`/Admin/Order/Update/${record.id}`} className="MorePrice">
              <Button shape="round" type="primary" >     
                暂无
              </Button>
            </Link>
          );
        }
      }
    },

    {
      title: "处理订单", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => { 
        if(record.statusId === 3){return (
          <Button shape="round" type="primary" icon ={<PlayCircleOutlined />} onClick={()=>processingOrder(record)}>     
          </Button>
        );}
        else{return(
          <Button shape="round" icon ={<PlayCircleOutlined />} disabled = {true} />     
        )}
      }
    },

      {
        title: "结束订单", 
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => { 
          if(record.statusId === 5){return (
            <Button shape="round" type="primary" icon ={<StopOutlined />} onClick={()=>finishOrder(record)}>     
            </Button>
          );}
          else{return(
            <Button shape="round" icon ={<VerticalAlignTopOutlined />} disabled = {true} />     
          )}
        }
      },

    {
      title: "追踪单号", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  
        if(record.trackNumber === null){
          return(
           <div>暂无</div>
            ) // 去同下link单号的界面去支付
        }
        else{
          return(  // 这里是一个爬虫 可以用来查看信息 与用户那个是一样的
          <Link to={`/Admin/Order/ATrackingInfo/${record.id}`} className="MoreTrack">
          <p>追踪: {record.trackNumber}</p>
          </Link>
          ) 
        }       
      }
    },    
  ];}

  let tableDataSource;
  if(props.record){
    tableDataSource = props.record.map(item => {
      return {
        ...item,
        key: item.id
      }
    })
  }
  return (
    <div className="AOrderFormContainer">
      <p>Hello Dear Below is your order record</p>
      <br/>
      <Table 
      dataSource={tableDataSource}
      columns={columns(approveOrder,processingOrder,finishOrder,closeOrder)}   // 要在这里绑定触发函数
      
       />
      
    </div>
  );
}