import React from "react";
import "./CreateOrder.css";
import { Form, Row, Col, Input, Button,AutoComplete,notification } from 'antd';
 
import { DownOutlined, UpOutlined } from '@ant-design/icons';
import "./CreateOrder.css";

 //const ReturnInfo = JSON.parse(JSON.stringify(postInfo)) 一种将obj直接复制存储到另外地址的方法

export default function CreateOrder(props){ 

  const [form] = Form.useForm(); 

  // 提交成功弹出框的内容

  const openNotification = () => {
    notification['success']({
      message: 'Successfully Add New Order',
      description:
        'You have successfully add an new order, please add parcel you wanna put into it in the table below',
      })
  };

  // onFinish 先要提交数据 然后更新home里的Records =>同时改变 dataSource=> 从而传递到orderRecord 去进行显示
  // 只用重新进去home界面时候 datasource才是直接从后台拿到的 否则会经过create 的修改
    
  const onFinish = values => {
    const postInfo = {userId:props.user.obj.uid,  // 需要拿到id  整形将要上传的数据
                      statusId: 1,   // 1 = New
                      price: null,
                      description: values.extraInfo,
                      receiverName:values.receiverName,
                      receiverPhone:values.receiverPhone,
                      receiverAddress:values.country +"/"+ values.province +"/"+ values.address01+"/"+values.address02,
                      receiverPostCode:values.receiverPostCode,
                      orderId: null,
                      trackNumber: null,
                      senderName: null,
                      senderAddress: null,
                      senderPhone: null,
                      senderPostCode: null,
                    };
    
    fetch("/api/order/create", { 
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify(postInfo),
    })
      .then(resp => resp.json())
      .then(data => {
        data.parcels = [];   // 必须要有 否则record就无法进行渲染
        let newRecord = [];
        newRecord = props.record.concat(data)   // 获得新的所有order 然后传递到上一级进行渲染
        return newRecord
      })
      .then(resp=>{
        console.log("new records entered?")
        console.log(resp)
        props.setRecord(resp);  // form.resetFields()  如何达到clear效果
      })
      .then(openNotification())  // 成功就弹框 否则报错
      .catch(error=>{})   // catch error 一般写啥 不知道 
  };

  const renderTitle = title => (
    <span>
      {title}
    </span>
  );
  
  const renderItem = (title) => ({
    value: title,
    label: (
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {title}
      </div>
    ),
  });


  /* 如果放弃自动输入改 手动输入 则下面可以不要 autocomplete 也一并删除即可*/

const country = ["美国","英国","澳大利亚","加拿大"] // 关于境外包裹的语言问题 选定国家后 省份 地址自定调整为 当地国家名字
const province = ["ontario","Quebec","Toronto","vancouver"]
const Opt_country = country.map(function(item,index){return {value:item}})
const Opt_province = province.map(function(item,index){return {value:item}})

  
  
    return (
      <Form
        form={form}
        name="InputTBCard"
        className="ant-advanced-search-form"
        onFinish={onFinish}
      >
        <Row gutter={24}>

        <Col span={6} key={1}>
            <Form.Item
              name="country"
              label="寄往国家"
              rules={[
                {
                  required: true,
                  message: '请选择国家或输入',
                },
              ]}
            >
            <AutoComplete
              style={{
                //width: 200,
              }}
              backfill="true"
              options={Opt_country}
              filterOption={(inputValue, option) =>
                option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
              }>
              <Input />  
              </AutoComplete>
            
            </Form.Item>
          </Col>

          <Col span={6} key={2}>
            
            <Form.Item
              name="province"
              label="所在省份"
              rules={[
                {
                  required: true,
                  message: '请输入省份',
                },
              ]}
            >
              <AutoComplete
              style={{
                //width: 200,
              }}
              options={Opt_province}
              filterOption={(inputValue, option) =>
                option.value.toUpperCase().indexOf(inputValue.toUpperCase()) !== -1
              }>
              <Input />  
              </AutoComplete>
            
            </Form.Item>
           
          </Col>

          <Col span={6} key={3}>
            <Form.Item
              name="address01"
              label="具体地址"
              rules={[
                {
                  required: true,
                  message: '请填写具体地址',
                },
              ]}
            >
              
                <Input placeholder="eg: Apt ###, ** rue/avenue" />   
             
            </Form.Item>
          </Col>
          
          <Col span={6} key={4}>
              
              <Form.Item
                name="address02"
                label="* 附加地址"
                rules={[
                  {
                    required: false,
                  },
                ]}
              >
                <Input />
              </Form.Item>
            
            </Col>

           
            <Col span={6} key={5}>
              <Form.Item
                name="receiverPostCode"
                label="收件邮编"
                rules={[
                  {
                    required: true,
                    message: '请输入邮编',
                  },
                ]}
              >
                  <Input />   
             
              </Form.Item>
            </Col>
            

          <Col span={6} key={6}>
              <Form.Item
                name="receiverName"
                label="收件人名"
                rules={[
                  {
                    required: true,
                    message: '请输入收件人姓名',
                  },
                ]}
              >
                <Input />
              </Form.Item>
            </Col>

            <Col span={6} key={7}>
              <Form.Item
                name="receiverPhone"
                label="联系方式"
                rules={[
                  {
                    required: true,
                    message: '请填写收件人号码',
                  },
                ]}
              >
                <Input placeholder="收件人电话或邮箱"/>
              </Form.Item>
            </Col>

            <Col span={6} key={8}>
            <Form.Item
              name="extraInfo"
              label="* 备注信息"
              rules={[
                {
                  required: false,
                  message: '***',
                },
              ]}
            >
              <Input />
            </Form.Item>
          </Col>

          </Row>

        <Row>
          <Col
            span={24}
            key={9}
            push={10}
            style={{
              textAlign: 'center',
            }}
          >
            <Button type="primary" htmlType="submit" >   
              Add
            </Button>
            <Button
              style={{
                margin: '0 8px',
              }}
              onClick={() => {
                form.resetFields();
              }}
            >
              Clear
            </Button>
          </Col>
        </Row>
      </Form>
    )}
  

  //<Cascader options={options} onChange={onChange} /> 用cascader 选地址是否更好

  //const ReturnInfo = JSON.parse(JSON.stringify(postInfo)) 一种将obj直接复制存储到另外地址的方法