
import React,{useState, useEffect}from "react";
import _get from "lodash/get";
 
import "./UpdateOrderInfo.css";
import UpdateOrderForm from "../UpdateOrderForm/UpdateOrderForm";
// import OrderSubmit from "../../Test/OrderSubmit";



export default function UpdateOrderInfo(props) {

  const [dataSource,setDataSource] = useState([])  // 初始值必须是list 才能在后面map 否则就是undefined
  const [thisOrderData,setThisOrderData] = useState() // 初始值必须是list 才能符合table标准

  const userId = props.user.obj.uid;     // 如果fetch写法是`/api/order/findAll/${userId}` 不需要toString() 如果是 “/api/order/findAll/” + userId 则需要toString()
  
  console.log('userId')
  console.log(userId)

  const thisOrderId = props.match.params.id;

  console.log(thisOrderId)


  // find/thisOrderId 只拿到这一条Order的信息； 而find 则会拿到某个user所有order中所有parcel信息 但是order的信息不够全面（比如没有付款信息）

  useEffect(()=>{
    fetch(`/api/order/find/${thisOrderId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // 拿到的是dictionary格式 Json ：[{"key":"123"}] 转换为obj 从而进行操作；
    .then(data => {
      setThisOrderData(data);  //[{}{}{}{}] 因为返回值data就是一个list
      console.log(data)
     }) 
  },[]) 

  // findAll/userId 拿到所有order信息 目的是韦后后面的search栏可以帮助user自动填充曾今写过的地址 都能搜索得到
  
  useEffect(()=>{
    fetch(`/api/order/findAll/${userId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // 拿到的是dictionary格式 Json ：[{"key":"123"}] 转换为obj 从而进行操作；
    .then(data => {
      setDataSource(data);  //[{}{}{}{}] 因为返回值data就是一个list
      console.log(data)
     }) 
  },[]) 

  const thisOrderStatus = _get(thisOrderData,"statusId")


  return(
    <div className="UpdateOrderInfo">
        <p> updateOrderInfo </p>  
        <div className="ChangeOrderInfo">
          <UpdateOrderForm user={props.user} thisOrderId={thisOrderId} thisOrderStatus={thisOrderStatus} thisOrderData={thisOrderData} dataSource ={dataSource}/>
        </div>
    </div>

  )
}
