import React from "react";
import _get from "lodash/get";
import {Table, Button,Tag,notification } from "antd";
import {EditOutlined,StopOutlined,PlusSquareOutlined,VerticalAlignTopOutlined} from '@ant-design/icons';
import { Link } from "react-router-dom";
 
import "./OrderRecord.css";

// antd table columns 内部绑定button的方式 非常重要！
//https://stackoverflow.com/questions/52767825/unable-to-update-the-state-when-button-inside-table-cell-clicked-react-ant-des

// 绑定成功 问题第一 是否成功传递过去，问题2 解决渲染问题 点击后应该触发渲染才对 即setState以下才可以 但是我拿到的值并不用来做任何处理


export default function OrderRecord(props) {

// props.setRecord 可以拿来用 点击提交后用来刷新整个界面

  // const userId = props.user.obj.uid;
 

  /***  提交完成后的提醒框 ** */
    const openNotification = (prop) => {
      notification['success']({
        message: '提交成功',
        description:
          '您的订单 ' + prop.id + ' 已成功提交 后台将立刻开始为您打包并核算运费'
        })
    };
  
    // 为了简化 应该直接有一个把0改成1的request；
    
  const SubmitOrder = (record)=>{

   fetch(`/api/order/submit/user/${props.user.obj.uid}/userorder/${record.id}`, { 
    method: "PUT",
    headers: {
    "Content-Type": "application/json"
    },
    })
    .then(resp => resp.json())
    .then(resp => openNotification(resp))
    .then(props.setRecord([]))      // 重新改变record值回到空值 然后由于record变化触发整体重新渲染 到上层use Effect时候 对比发现【record.length】发生了改变 重新渲染全部信息 就拿到了最新的信息
  }

  const FinishOrder = (record)=>{
    fetch(`/api/order/finish/user/${props.user.obj.uid}/userorder/${record.id}`, { 
      method: "PUT",
      headers: {
      "Content-Type": "application/json"
      },
      })
      .then(resp => resp.json())
      .then(resp => openNotification(resp))
      .then(props.setRecord([])) 
  }

  const columns = (SubmitOrder,FinishOrder)=>{
    return [
    {
      title: "订单号",
      dataIndex: "id",
      key: "id",
      render: text => <div>{text}</div>,
    },
    
    {
      title: "内含包裹",
      dataIndex: "parcelString",
      key: "parcelString",
      render: text => <div>{text}</div>,
    },
   

    {
      title: '订单状态',
      key: 'statusId',
      dataIndex: 'statusId',
      render: (text, record, index) => { 
            let status = ""
            let color = ""  
            if (record.statusId === 1){
              color = "defaut";
              status = "未完成"
            } else if(record.statusId === 2){
              color = "success";
              status = "已提交"
            } else if(record.statusId === 3){
              color = 'error';
              status = "关闭订单"
            }
             else if(record.statusId === 4){
              color = "warning";
              status = "待付款"
            }
            else if(record.statusId === 5){
              color = "processing";
              status = "在处理"
            }
            else if(record.statusId === 6){
              color = "defaut";
              status = "已结束"
            }
            return (
              <Tag color={color} key={index}>
                {status}
              </Tag>
            );
          }},
  

    {
      title: "增删包裹",  // 这里只需要拿到  record.id 并去跳转就可以了
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  // 这里的record是指传入表中的data 即 datasource ！ 参数只能是这里面的内容
        if(record.statusId === 1){return (
          <div>
              <Link to={`/Business/OrderHome/AddParcel/${record.id}`} className="LearnMore">
              <PlusSquareOutlined/>
              </Link>
          </div>
        );} else{return(
          <div>
                <StopOutlined />   
          </div>
        )}
      }
    },

    {
      title: "修改订单",  // 这里只需要拿到  record.id 并去跳转就可以了
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  // 这里的record是指传入表中的data 即 datasource ！ 参数只能是这里面的内容 record = props.dataSource
        if(record.statusId === 1){return (
          <div>
              <Link to={`/Business/OrderHome/OrderInfo/${record.id}`} className="More">
              <EditOutlined/>
              </Link>
          </div>
        );}
        else{return(
          <div>
                <StopOutlined />   
          </div>
        )}
      }
    },

    {
      title: "提交订单", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => { 
        if(record.statusId === 1){return (
          <Button shape="round" type="primary" icon ={<VerticalAlignTopOutlined />} onClick={()=>SubmitOrder(record)}>     
          </Button>
        );}
        else{return(
          <Button shape="round" icon ={<VerticalAlignTopOutlined />} disabled = {true} />     
        )}
      }
    },

    {
      title: "付款",  // 这里只需要拿到  record.id 并去跳转就可以了
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  // 这里的record是指传入表中的data 即 datasource ！ 参数只能是这里面的内容 record = props.dataSource 付款状态后面需要修改
        if(record.statusId === 1 || record.statusId === 2 || record.statusId === 3){return (   
              <Button shape="round" type="primary" disabled = {true} >     
              付款
              </Button>  
        );}
        else if(record.statusId === 4){return (
              <Link to={`/Business/OrderHome/PriceInfo/${record.id}`} className="MorePrice">
              <Button shape="round" type="primary" >     
              付款
              </Button>
              </Link>
        );}
        else {return (
          <div>
              <Link to={`/Business/OrderHome/PriceInfo/${record.id}`} className="MorePrice">
              <Button shape="round" type="default" >     
              已付款
              </Button>
              </Link>
          </div>
        );}
      }
    },

    {
      title: "查询", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => {  
        if(record.trackNumber === null){
          return(<div>暂无信息</div>)
        }
        else{
          return(
          <Link to={`/Business/OrderHome/TackingInfo/${record.id}`} className="MoreTrack">
          <p>{record.trackNumber}</p>
          </Link>
          ) 
        }       
      }
    },   
    {
      title: "确认收货", 
      dataIndex: "action",
      key: "action",
      render: (text, record, index) => { 
        if(record.statusId === 5){return (
          <Button shape="round" type="primary" onClick={()=>FinishOrder(record)}>  
            确认   
          </Button>
        );}
        else if(record.statusId === 6){
          return(
            <span>已结束</span>   
          )
        }
        else{return(
          <Button shape="round" type="primary" disabled = {true} >    
            确认 
          </Button>
        )}
      }
    }, 
  ];}

  // 筛选所有不为结束的订单

  var historyData = props.dataSource.filter(function(rec){
    if(rec){return rec.statusId !== 6}
    else{return([])}
  })

  let tableDataSource;
  if(historyData){
    tableDataSource = historyData.map(item => {
      return {
        ...item,
        key: item.id
      }
    })
  }
  return (
    <div>
      <p>Hello Dear Below is your order record</p>
      <br/>
      <Table 
      dataSource={tableDataSource}
      //columns={columns}
      columns={columns(SubmitOrder,FinishOrder)}   // 要在这里绑定触发函数
      expandable={
        {
          expandedRowRender: (record) => {
            return(
            <div>
              <p>收件人 &nbsp; {record.receiverName} &nbsp;  {record.receiverPhone}</p>
              <p>地址 {record.receiverAddress } &nbsp; {record.receiverPostCode}</p>
              <p>{record.description}</p>
            </div>
            )}
        }
      }
      
       />
      
    </div>
  );
}
