import React,{useState}from "react";
import _get from "lodash/get";
import {Table, Form, Popover,Button,Input,notification } from "antd";
import {EditOutlined} from '@ant-design/icons';
import "./HomePersonInfo.css";
 
const FormItem = Form.Item;

export default function HomePersonalInfo(props) {

  const email = props.record.map(item => {return item.email})  // 用来在重置密码时候提醒对方收件邮箱

  /* Pop 气泡卡片 点击发送email请求并同时关闭这个弹框 */

  const [resetPopState, setResetPopState] = useState({visible:false,})

  const resetPassword =()=>{
    console.log("reset password")  // 传递请求
    setResetPopState({visible:false,})
}
  const handleVisibleChange = visible => {
    setResetPopState({ visible });
};

  /* 更新成功后的弹框提示  */
  
    const openNotification = () => {
        notification['success']({
          message: 'Successfully update user information',
          description:
            'You have successfully unpdate your personnal information',
          })
      };

     
    const FormEdit=()=>{
  
      const onFinish = values => {
        console.log('Received values of form: ', values); // 在这里加入如何进行update数据 最好再次setState一下 就可以重新渲染了
        
      const newUserInfo = {
          uid:props.user.obj.uid,  // 需要拿到id
          username:props.user.obj.username,// ID 和 用户名都不可以改变所以直接拿参数使用就好了
          phone:values.phone,
          email:values.email,
          role:props.user.obj.role,
          address: values.address
        };
  
      fetch("/api/user/update", { 
        method: "PUT",
        headers: {
        "Content-Type": "application/json"
        },
        body: JSON.stringify(newUserInfo),
        })
        .then(resp => resp.json())
        .then(data=> props.setRecord([data]))
        .then(openNotification()) // 成功添加完毕后才能清空 否则不会触发  另外单独点击clear也有一样的效果 但是是再写错想要清除全部的情况下进行 如何自动清空内容？
      };
      return(
      <Form onFinish={onFinish}
      initialValues={{
      }}>
  
        <FormItem 
        name="email"
        rules={[
          {
            required: true,
            message: 'Please input your email!',
          },
        ]}>
            <Input placeholder="Email" />   
        </FormItem>
  
        <FormItem  
        name="phone"
        rules={[
          {
            required: true,
            message: 'Please input your phoneNumber!',
          },
        ]}>
            <Input placeholder="Phone" />    
        </FormItem>
  
        <FormItem 
        name="address"
        rules={[
          {
            required: false,
          },
        ]}>
          <Input placeholder="Add Address" />
        </FormItem>
  
        <FormItem>
          <Button type="primary" htmlType="submit">  
            Submit
          </Button> 
        </FormItem>
      </Form>
    );}
  
    const columns = [
      {
        title: "用户名",
        dataIndex: "username",
        key: "name"
      },
      {
        title: "邮箱",
        dataIndex: "email",
        key: "email"
      },
      {
        title: "电话号码",
        dataIndex: "phone",
        key: "phone"
      },
      {
        title: "密码",
        dataIndex: "fakePassword",
        key: "fakePassword"
      },
      {
        title: "常用地址",
        dataIndex: "address",
        key: "address"
      },
      {
        title: "信息更新",
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => {
          return (
            <div>
              <Popover
                content={FormEdit()}   
                title={props.user.obj.username + " edit"}  //{props.user.obj.username + " edit"} // 这里可以传递参数 props.user.obj   const objectId = _get(item, "id");
                trigger="click"
                placement="left"
              >
                <Button icon={<EditOutlined />} />
              </Popover>
            </div>
          );
        }
      },
  
      {
        title: "更换密码",
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => {
          return (
            <div>
              <Popover
                content={
                <div>
                <p> 重置密码的链接将发送到你的 {email} 邮箱</p>
                <p> 请确认发送 并注意查收</p>
                <a onClick={resetPassword}>确认发送</a>
                </div>} 
                title={"亲爱的"+ props.user.obj.username + "你好" }  //{props.user.obj.username + " edit"} // 这里可以传递参数 props.user.obj   const objectId = _get(item, "id");
                trigger="click"
                placement="left"
                visible={resetPopState.visible}
                onVisibleChange={handleVisibleChange}
              >
                <Button icon={<EditOutlined />} />
              </Popover>
            </div>
          );
        }
      }
      
    ];

  let tableDataSource;

  if(props.record){
    tableDataSource = props.record.map(item => {
      return {
        ...item,
        key: item.uid
      }
    })
  }
  
    return (
      <div>
        <p>Hello Dear you already login below is your information</p>
        <br/>
        <Table dataSource={tableDataSource} columns={columns} />
      </div>
    );


}
/* 传递变量或者文字时候的转行富豪  "\r\n"
 title={props.user.obj.username + "你好" + "\r\n"
                        +"重置密码的链接将发送到你的"+email+"邮箱"+ "\r\n"
                        +"请确认发送 并注意查收"} 
*/