import React,{useState,useEffect} from 'react';
import AlreadyCommit from '../AlreadyCommit/AlreadyCommit';
import NoCommit from '../NoCommit/NoCommit';


export default function HistoryHome(props) {


  const userId = props.user.obj.uid.toString();
  const userName = props.user.obj.username.toString();

  const [OrderInfo,setOderInfo] = useState([]) 
  const [historyInfo,setHistoryInfo] = useState([])

  useEffect(()=>{
    fetch(`/api/order/findAll/${userId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())  
    .then(data => {
      setOderInfo(data);  
     }) 
  },[setOderInfo.length]) 
  
  useEffect(()=>{
    fetch(`/api/orderhistory/findAll/info/${userId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())  
    .then(data => {
      setHistoryInfo(data);  
     }) 
  },[setHistoryInfo.length]) 
  
  
  var finishedOrder = OrderInfo.filter(function(rec){
    if(rec){return rec.statusId === 6}
    else{return([])}
  })

   // 所有完成状态的订单ID list

  var finishedOrderId = finishedOrder.map(item=>item.id)  

  // 所有有评论的订单ID list

  var orderIdWithCommit = historyInfo.map(function(his){    
    if(his){return his.orderHistory.userOrderId}
    else{return([])} 
  })

// finishedORderID 与 orderIdWithCommit 的差集

  var orderIdWithOutCommit = finishedOrderId.filter(function(v){ return orderIdWithCommit.indexOf(v) == -1 })

// 拿到所有需要进行评价的record信息

var recordNeedCmt = finishedOrder.map(function(fini){
  if(fini){
    if(orderIdWithOutCommit.indexOf(fini.id)!== -1){return fini}
      // 如果不写返回值 就会成为undefined   如何直接移除这一项呢？ 去 table当中改变吗
  }
  else{return([])}
}).filter(Boolean)  // 删除所有undefine null 0 false 




return(
      <div className="TBList_Container"> 
        <NoCommit userName={userName} userId={userId} recordNeedCmt={recordNeedCmt} />  
        <AlreadyCommit  userName={userName} userId={userId} historyInfo={historyInfo} />
      </div> 
)
  }

  /*
  (4) [{…}, {…}, {…}, {…}]
0: {}
1: {id: 3, userId: 5, statusId: 6, price: 346, description: "zjeshi xinxi", …}
2: {id: 4, userId: 5, statusId: 6, price: 350, description: "some book and cloth to canada", …}
3: {}
  */