import React from "react";
import "./Business.css";
import BusinessBeforeLogIn from "../BusinessBeforeLogIn/BusinessBeforeLogIn";
import SDashBoard from "../BusinessAfterLogIn/SDashBoard/SDashBoard";

export default function Business(props) {

  // 是否存在其他可能的错误代码 200 500 之外
  if (props.user.code === 200 && props.user.obj.role === "user"){
    return(<SDashBoard user = {props.user}/>)
   } 
  else{
    return(<BusinessBeforeLogIn/>)}
}


