import React,{useState,useRef}from "react";
import _get from "lodash/get";
import {Table, Form, Popover,Button,Input,notification,Space, } from "antd";
import { Link } from "react-router-dom";
import {EditOutlined,SearchOutlined} from '@ant-design/icons';
import Highlighter from 'react-highlight-words';
import "./AUserInfoForm.css";

const FormItem = Form.Item;

export default function AUserInfoForm(props) {

 //  const email = props.record.map(item => {return item.email})  // 用来与邮箱账户关联 点击即可打开邮箱也可以复制

  /* 信息更新成功后的弹框提示  */
  
    const openNotification = () => {
        notification['success']({
          message: '更新信息成功',
          description:
            '您已经成功更新该用户的信息'
          })
      };

    
  /*   信息修改 弹框  */
    const FormEdit=(prop)=>{

      const onFinish = values => {

      const newUserInfo = {
          uid:prop.uid,  // 需要拿到id
          username:prop.username,// ID 和 用户名都不可以改变所以直接拿参数使用就好了
          phone:values.phone,
          email:values.email,
          role:"user",
          wxId:values.wxId,
          address: values.address
        };
  
      fetch("/api/user/update", { 
        method: "PUT",
        headers: {
        "Content-Type": "application/json"
        },
        body: JSON.stringify(newUserInfo),
        })
        .then(resp => resp.json())
        .then(data=> props.setRecord([data]))
        .then(openNotification()
        ) // 成功添加完毕后才能清空 否则不会触发  另外单独点击clear也有一样的效果 但是是再写错想要清除全部的情况下进行 如何自动清空内容？
      };
      return(
      <Form onFinish={onFinish}
      initialValues={{
      }}>
  
        <FormItem 
        name="email"
        rules={[
          {
            required: true,
            message: 'Please input your email!',
          },
        ]}>
            <Input placeholder="Email" />   
        </FormItem>
  
        <FormItem  
        name="phone"
        rules={[
          {
            required: true,
            message: 'Please input your phoneNumber!',
          },
        ]}>
            <Input placeholder="Phone" />    
        </FormItem>

        <FormItem 
        name="wxId"
        rules={[
          {
            required: true,
            message: 'Please input weChat ID!',
          },
        ]}>
            <Input placeholder="WeChat ID" />   
        </FormItem>
  
        <FormItem 
        name="address"
        rules={[
          {
            required: false,
          },
        ]}>
          <Input placeholder="Add Address" />
        </FormItem>
  
        <FormItem>
          <Button type="primary" htmlType="submit">  
            Submit
          </Button> 
        </FormItem>
      </Form>
    );}
  
    /*所有的搜索部分需要的状态  */

    const [searchText, setSearchText] = useState("");
    const [searchedColumn, setSearchedColumn] = useState("");
    var searchInput = useRef(null);  //const searchInputReference = {};  searchInputReference.searchInput 直接拿到 这是蔡指导的方法
   
  
    const getColumnSearchProps = dataIndex => ({
      filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <div style={{ padding: 8 }}>
          <Input
            ref={node => {
              searchInput = node;
            }}
            placeholder={`Search ${dataIndex}`}
            value={selectedKeys[0]}
            onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
            onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
            style={{ width: 188, marginBottom: 8, display: 'block' }}
          />
          <Space>
            <Button
              type="primary"
              onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
              icon={<SearchOutlined />}
              size="small"
              style={{ width: 90 }}
            >
              Search
            </Button>
            <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
              Reset
            </Button>
          </Space>
        </div>
      ),
      filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
      onFilter: (value, record) =>
        record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
      onFilterDropdownVisibleChange: visible => {
        if (visible) {
          setTimeout(() => searchInput.select());   
        }
      },
      render: text =>
        searchedColumn === dataIndex ? (
          <Highlighter
            highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
            searchWords={[searchText]}
            autoEscape
            textToHighlight={text.toString()}
          />
        ) : (
          text
        ),
    });
  
    const handleSearch = (selectedKeys, confirm, dataIndex) => {
      confirm();
  
      setSearchText(selectedKeys[0])
      setSearchedColumn(dataIndex)
    };
  
    const handleReset = clearFilters => {
      clearFilters();
      setSearchText("")
    };
  


    /* column添加搜索展开 */
    const columns = [
        {
            title: "用户ID",
            dataIndex: "uid",
            key: "uid",
            ...getColumnSearchProps('uid'),
          },
      {
        title: "用户名",
        dataIndex: "username",
        key: "name",
        ...getColumnSearchProps('username'),
      },
      {
        title: "邮箱",
        dataIndex: "email",
        key: "email",
        ...getColumnSearchProps('email'),
      },
      {
        title: "电话号码",
        dataIndex: "phone",
        key: "phone",
        ...getColumnSearchProps('phone'),
      },
      {
        title: "微信ID",
        dataIndex: "wxId",
        key: "wxId",
        ...getColumnSearchProps('wxId'),
      },
      {
        title: "常用地址",
        dataIndex: "address",
        key: "address",
      },
      {
        title: "信息更新",
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => {
          return (
            <div>
              <Popover
                content={FormEdit(record)}   
                title={"编辑" + record.username+ "的信息"}  //{props.user.obj.username + " edit"} // 这里可以传递参数 props.user.obj   const objectId = _get(item, "id");
                trigger="click"
                placement="left"
              >
                <Button icon={<EditOutlined />} />
              </Popover>
            </div>
          );
        }
      },
  
      {
        title: "更换密码",
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => {
          return (
            <div>
              <a>更换密码</a>
            </div>
          );
        }
      },
      {
        title: "查看所有信息",
        dataIndex: "action",
        key: "action",
        render: (text, record, index) => { 
            return(
            <div>
            <Link to={`/Admin/UserInfo/${record.uid}`} className="MoreTrack">
            查看{record.uid}
            </Link>
            </div>
            )      
        }
      }

      
    ];

  let tableDataSource;

  if(props.userRecord){
    tableDataSource = props.userRecord.map(item => {
      return {
        ...item,
        key: item.uid
      }
    })
  }
  
    return (
      <div>
        <p>用户个人信息</p>
        <br/>
        <Table dataSource={tableDataSource} columns={columns} />
      </div>
    );

}