import React, { useContext } from "react"; //{ PureComponent }
import { Formik } from "formik";
import DisplaySignInForm from "./DisplaySignInForm";
//import { useHistory } from "react-router-dom";
import { Link } from "react-router-dom";
import { Result, Button,Row, Col,Alert,Layout } from 'antd';
//import { dateFormat,timeFormat} from "./FieldFormats";
//import moment from "moment";
import * as Yup from "yup";
import UserContext from "../Context/UserContext";
import "./SignIn.css";
 
// !!! 自定义的组件名称必须以大写字母开头。小写字母开头的组件视为原生 DOM 标签


const validationSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, "* at least 2 characters")
    .max(100, "* can't be longer than 100 characters")
    .required("* Name is required"),
  
  password: Yup.string()
  .min(2, "* at least 2 characters")
  .max(100, "* can't be longer than 100 characters")
  .required("* Password is required"),
});


const initialValues = {
  name: "",
  password: ""
};


function SignIn() {

  const [userState, setUserState] = useContext(UserContext);
  
  
  console.log("signIn-userState 的内容")
  console.log(useContext(UserContext));
  console.log(userState)   // {code:200, obj{...},message"..."" 
  // console.log(userState.obj) //{password:null, username:"Qingqing",Authorties:}  // 进入了obj
  
  function SignInImage() {
    return(
      <div className="signInImg">
        <h2 className="signInTitle01"> 一闪转运</h2>
        <h2 className="signInTitle02"> 尽享国淘</h2>
      </div>
    )
  }


  const SignInverify = props => {
    fetch('/api/doLogin', {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded"
      },
      body: `username=${props.name}&password=${props.password}` // 修改 统一用email登陆
    })
      .then(resp => resp.json())
      .then(data => {
        setUserState(data);
        console.log("get return value")
        console.log(data.code); // 200
      });
  };

  const LogOut = () => {
    fetch('/api/logout', {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded"
      },
    })
      .then(resp => resp.json())
      .then(data => {
        setUserState(data);
        console.log("get logout value")
        console.log(data); // 200
      });
  };


  // 判定 obj{} 是空值 不能直接写 userState == {}. https://stackoverflow.com/questions/679915/how-do-i-test-for-an-empty-javascript-object
  // 或者用Rambda isEmpty 判断 R.isEmpty({});


  if (Object.keys(userState).length === 0){return (
    <Row className="SignInContainer">
      <Col span={12}>
        <SignInImage/>
      </Col>
      <Col className="SignInForm" span={12}>
        <Formik
          initialValues={initialValues}
          validationSchema={validationSchema}
          onSubmit={(values, { setSubmitting, resetForm }) => {
            SignInverify(values);
            // 见38 useHistory history.push('/Qingqing'); https://stackoverflow.com/questions/42701129/how-to-push-to-history-in-react-router-v4
            setSubmitting(true);
            setTimeout(() => {
              resetForm(); //重置表单
              setSubmitting(false); // 允许提交按钮生效
            }, 1000);
          }}
          render={DisplaySignInForm}
        />
      </Col>
    </Row>
  );} 
  else if (userState.code === 500 ){
    // alert(" UserName or password is not correct, please enter correct information")
    return(  
   
      <div className="SignInContainer">
        <Row>
          <Col className="signInAlert"span={24}>
            <Alert
              key={1} 
              message="Error"
              description=" UserName or password is not correct, please enter correct information"
              type="error"
              showIcon
            />
          </Col>
        </Row>

        <Row>
          <Col span={12}>
              <SignInImage/>
          </Col>
          <Col className="SignInForm" span={12}>
            <Formik
              key={2} 
              initialValues={initialValues}
              validationSchema={validationSchema}
              onSubmit={(values, { setSubmitting, resetForm }) => {
                SignInverify(values);
                // 见38 useHistory history.push('/Qingqing'); https://stackoverflow.com/questions/42701129/how-to-push-to-history-in-react-router-v4
                setSubmitting(true);
                setTimeout(() => {
                  resetForm(); //重置表单
                  setSubmitting(false); // 允许提交按钮生效
                }, 1000);
              }}
              render={DisplaySignInForm}
            />
          </Col>
        </Row>
      </div>
    )
  } 
  else if(userState.message === "login successfully"){

    return(
      <Result
      key={1}
      status="success"
      title="登陆成功!"
      subTitle="亲爱的顾客您好 欢迎使用一闪服务"
      extra={[
        
        <Button key={2} type="primary" key="console01" onClick = {LogOut}>
          退出
        </Button>,

        <Link key={3} exact="true" to={`/Business`} className="SDashBoard">
        <Button key={4} type="primary" key="console02">
          我的订单
        </Button>
        </Link>
      ]}
    />
    )
}
  else if(userState.message === "logout successfully"){
//  如果500那个Alert形式成功 就把这里也替换一下就行
    return(
      <div className="SignInContainer">
        <Row>
          <Col className="signInAlert"span={24}>
            <Alert
              key={1} 
              message="Success Tips"
              description=" you have successfully logout!"
              type="success"
              showIcon      
            />
          </Col>
        </Row>

        <Row>
          <Col span={12}>
              <SignInImage/>
          </Col>
          <Col className="SignInForm" span={12}>
            <Formik
              key={2} 
              initialValues={initialValues}
              validationSchema={validationSchema}
              onSubmit={(values, { setSubmitting, resetForm }) => {
                SignInverify(values);
                // 见38 useHistory history.push('/Qingqing'); https://stackoverflow.com/questions/42701129/how-to-push-to-history-in-react-router-v4
                setSubmitting(true);
                setTimeout(() => {
                  resetForm(); //重置表单
                  setSubmitting(false); // 允许提交按钮生效
                }, 1000);
              }}
              render={DisplaySignInForm}
            />
          </Col>
        </Row>
      </div>
     
    )
  }
}

export default SignIn;



