import React, {useContext,useState, Fragment} from "react"; //{ PureComponent }
import { Formik } from "formik";
import DisplayRegisterForm from "./DisplayRegisterForm";
import { Alert,Col,Row,Layout } from 'antd';
import "./Register.css"
//import { dateFormat,timeFormat} from "./FieldFormats";
//import moment from "moment";
import * as Yup from "yup";
 

const phoneRegExp = /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/;
const emailRegExp = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;


// schema 可以和 valid 共存 但是会override 过缺少 就直接走valid 
const validationSchema = Yup.object().shape({
  name: Yup.string()
    .min(2, "* at least 2 characters")
    .max(100, "* can't be longer than 100 characters")
    .required("* Name is required"),
  email: Yup.string()
    .matches(emailRegExp,"* Must be a valid email address")
    .max(100, "* must be less than 100 characters")
    .required("* Email is required"),
  phone: Yup.string()
    .matches(phoneRegExp, "*Phone number is not valid")
    .required("* Phone number is required"),
  password: Yup.string()
    .min(6, "* at least 6 characters")
    .max(100, "* can't be longer than 100 characters")
    .required("* Password is required"),
  passwordConfirmation: Yup.string()
  .oneOf(
    [Yup.ref("password"), null],
    "* Passwords must match"
  )
  .required("* Password is required"),
});


const initialValues = {
  name: "",
  email:"",
  phone: "",
  password: ""
};


function Register(){

  const [register,setRegister] = useState("")

  
 // const [userState, setUserState] = useContext(UserContext); 理论上说register成功后应该重新登陆才行 不是直接登陆 所以不会改变Context的值
  
 const data=(props)=> JSON.stringify({'username':props.name,'password':props.password,'email': props.email, 'phone': props.phone})
 
  const RegisterVerify=(props)=>{
    fetch("/api/register", { // 修改网址
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: data(props),
  })
    .then(resp => resp.json())
    .then(data => {
      setRegister(data)
      console.log(data)
    });
  }

  const AlterRegister = ()=>{
    if(register === ""){
    return(
      <Alert
      message="新用户注册"
      description="请输入您的个人信息用以注册 我们将保护您的隐私 不会泄露个人信息"
      type="info"
      showIcon
     // closable
    />
    )}
    else if(register.code === 500 ){
      return(
      <Alert
      message= {register.message} 
      description=" 该用户已经存在 该邮箱已经被注册 请登陆或更换邮箱"
      type="error"
      showIcon
      closable/>
      )
    }
    else {
      return(
        <Alert
        message= {"亲爱的 "+ register.username}
        description=" 注册成功 邮件已发送到您的邮箱 请前往激活"
        type="success"
        showIcon
        closable/>
        )
    }
   }

 return (
    <Layout className="RegisterContainer">
      <Row className="alterRegister" >
        <Col span={24}>
        <AlterRegister/>
        </Col>
      </Row>
      <Row className="RegisterFormContainer">
        <Col span={8}></Col>
        <Col className ="ResigterForm" span={8}>
          <Formik
            initialValues={initialValues}
            validationSchema={validationSchema}
            onSubmit={(values,{setSubmitting, resetForm})=>{
              RegisterVerify(values);
                // 见38 useHistory history.push('/Qingqing'); https://stackoverflow.com/questions/42701129/how-to-push-to-history-in-react-router-v4
                
                setSubmitting(true);
                setTimeout(() => {
                  resetForm(); //重置表单
                  setSubmitting(false); // 允许提交按钮生效
                }, 500);
                
            }}
            render={DisplayRegisterForm}
          />
      </Col>
      <Col span={8}></Col>
    </Row>
    </Layout>
  );
}

export default Register;


//注册出现了没有返回值的问题 似乎不能成功注册 但是再次用同样的邮箱注册 又会说已经被注册了
