import React,{useState,useEffect} from "react";
import "./TBHome.css";
import TBForm from "../TBForm/TBForm"
import TBcard from "../TBcard/TBCard";
import { Row } from "antd";

export default function TBHome(props) {

  const [record,setRecord]= useState([])

  const userId = props.user.obj.uid.toString();

  useEffect(()=>{
    fetch("/api/parcel/findAll/userid/"+ userId, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // 拿到的是dictionary格式 Json ：[{"key":"123"}] 转换为obj 从而进行操作；
    .then(data => {
      setRecord(data);  //[{}{}{}{}]
      console.log("orginal record catch from database:")
      console.log(record)
     }) 
  },[]) 



  // 首先 fetch去拿到所有淘宝的包裹信息组成{} 作为初始值 传递到两个 component 进行渲染；
  // 每次点击submit就传递新值 所以要写useEffect 去拿到最新的值去渲染TBcard
  // 个人认为这里需要一个勾子把两边勾起来 但是只存在一个回传 所以先试试单纯的传递勾子试试看

  // 在这里设置接受props参数 并传递进去

  // 外观需要优化 不够美  

  // 上下两个DIV宽度无法对齐的问题
  return ( 
      <div className="TBList_Container"> 
        <TBForm user={props.user} record={record} setRecord={setRecord} />  
        <TBcard  user={props.user} record={record} setRecord={setRecord}  />
      </div> 
  )
}
