import React,{useState, useEffect}from "react";
import {Button,notification } from 'antd';
import { Link } from 'react-router-dom';
import _get from "lodash/get";
 
import "./ATracking.css";


export default function ATacking(props) {

  const [dataSource,setDataSource] = useState([])    //  某一用户名字下所有包裹 （包括有分配订单和没有分配订单的包裹）

  // const userId = props.user.obj.uid.toString();

  const thisOrderId = props.match.params.id; 

  useEffect(()=>{
    fetch(`/api/order/find/${thisOrderId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   
    .then(data => {
        setDataSource(data)
     })
  },[])

  const TrackOrder = ()=>{console.log("TrackOrder")}
 
    return (
      <div>
        <p>这位客户的包裹发出单号为 {dataSource.trackNumber}  预计到达日期为 {dataSource.expectDeliveryDate} </p>

        <p> 请查询 </p>
        
        <Button id="submitOrder" size="large" style={{ float: 'center', margin: 5}} onClick={TrackOrder}> 
            查询
        </Button>

        <Link to="/Business/OrderHome">
        <Button size="large" style={{ float: 'center', margin: 5 }}>
            返回
        </Button>
        </Link>        
      </div>
    );
}
