import React,{useState,useEffect}from "react";
import _get from "lodash/get";
import {Table, Form, Popover,Button,Input,notification } from "antd";
import {EditOutlined} from '@ant-design/icons';
import HomePersonInfo from "../HomePersonInfo/HomePersonInfo";
 
import "./HomeAfterLogIn.css";

// 写 onfinish
const FormItem = Form.Item;

//const [form] = Form.useForm();   /// ?


export default function HomeAfterLogIn(props) {

  const [record,setRecord] = useState([])  //props.user.obj.uid
 
  // record 必须写成list才可以放到table之中

  useEffect(()=>{
    fetch("/api/user/"+ props.user.obj.username, { 
      method: "Get",
      headers: {
        "Content-Type": "application/json"
      },
    })
      .then(resp => resp.json())
      .then(data => {
        setRecord([data]);   // 因为返回值是一个{} 所以外面加个[] 才符合table的格式
      });
  },[]) 

  record.map(item=>item.fakePassword = "*******")
  console.log("map record")
  console.log(record)


  return (
    <div className="OrderHome">
    
        <p> OrderHome </p>  
        <HomePersonInfo user={props.user} record={record} setRecord={setRecord} />  
    </div>
  ) 
}
