import React, {useState,useEffect} from "react"; //{ PureComponent }
import { Redirect } from 'react-router';
import { Formik } from "formik";
import { Form, Field } from "formik";
import {
    AntInput
    // AntDatePicker,AntSelect,AntTimePicker
  } from "../AntPureComponent/CreateAntFields";
import * as Yup from "yup";
import { Link } from "react-router-dom";
import { Button,Result} from 'antd';
 
import { values } from "lodash";

const emailRegExp = /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/;

const validationSchema = Yup.object().shape({
    email: Yup.string()
      .matches(emailRegExp,"* Must be a valid email address")
      .max(100, "* must be less than 100 characters")
      .required("* Email is required"),
  });

const EmailAdress = ({ handleSubmit, values, submitCount }) => (
    <Form className="form-container" onSubmit={handleSubmit}>
      <div ><p className ="LabelStyle">请输入您用于注册的邮箱:</p></div>
      <Field
        component={AntInput}
        name="email"
        type="email"
        //label="Email"
        // validate={validateEmail}
        submitCount={submitCount}
        hasFeedback
      />
        <div className="submit-container">
        <button className="ant-btn ant-btn-primary" type="primary" shape="round">
            提交
        </button>
        </div>
    </Form>
  );

  const sendEmail = (prop)=>{console.log("填写发送邮件的指令"+ prop)}

  const initialValues = {
    email:"",
  };

export default function ForgetPassword() {

    const [emailOfRgst,setEmailOfRgst] = useState("")

    if (emailOfRgst === "") 
            {return (
                <Formik
                initialValues={initialValues}
                validationSchema={validationSchema}
                onSubmit={(values,{setSubmitting, resetForm})=>{
                    setEmailOfRgst(values.email)
                    sendEmail(values.email)
                    setSubmitting(true);
                    setTimeout(() => {
                        resetForm(); //重置表单
                        setSubmitting(false); // 允许提交按钮生效
                    }, 500);        
                }}
                render={EmailAdress}
                />
            )}
    else if(Object.keys(emailOfRgst).length > 0){ 
       return(
        <Result
        status="success"
        title="邮件发送成功"
        subTitle={"亲爱的用户 您的密码找回信息已经发送到您的邮箱"+ emailOfRgst+"请前往找回密码"}
        extra={[        
          <Link exact to={`/`}>
          <Button type="primary">
            返回主页
          </Button>
          </Link>
        ]}
      />
       )
        }
}