import React from "react";
import "./Home.css";
import HomeBeforeLogIn from "../HomeBeforeLogIn/HomeBeforeLogIn";
import HomeAfterLogIn from "../HomeUser/HomeAfterLogIn/HomeAfterLogIn";
import ErrorPage from "../../ErrorPage/ErrorPage";
import AdminDashBoard from "../HomeAdmin/AdminDashBoard/AdminDashBoard";


export default function Home(props) {
  
  // console.log("enter home" + props.user.obj.username)

  // 是否存在其他可能的错误代码 200 500 之外
  if (Object.keys(props.user).length === 0 || props.user.code === 500 || props.user.message ==="logout successfully" ){
    return(<HomeBeforeLogIn/>)}   // xiugaiwei kongzhi 
  else if(props.user.code === 200 && props.user.obj.role === "user"){
    return(<HomeAfterLogIn user = {props.user}/>)}
  else if (props.user.code === 200 && props.user.obj.role === "admin"){
    return(<AdminDashBoard user = {props.user}/>)}
  else {return(<ErrorPage/>)}
} 

