import React,{useState,useEffect}from "react";
import CreateOrder from "../CreateOrder/CreateOrder";
import OrderRecord from "../OrderRecord/OrderRecord";
import "./OrderHome.css";
 
//import { Row, col } from "antd";

export default function OrderHome(props) {

  console.log("orderHome参数传递")
  console.log(props.user.obj.uid)   // number

  const userId = props.user.obj.uid.toString();

  const [record,setRecord] = useState([])  // 初始值必须是list 才能符合table标准

  useEffect(()=>{
    fetch("/api/order/find/user/"+ userId, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // 拿到的是dictionary格式 Json ：[{"key":"123"}] 转换为obj 从而进行操作；
    .then(data => {
      setRecord(data);  //[{}{}{}{}] 因为返回值data就是一个list
      console.log("value of box")
      console.log(record)
     }) 
  },[record.length])  // https://github.com/facebook/react/issues/15548 useEffect 根据length判定


  record.forEach(
    function(item){
      const Pid = item.parcels.map(({id})=> "*"+id)
      item.parcelString = Pid.join('\r\n')   // 新创建了一个叫ParcelString的关键词 专门放list信息 以防后面还需要再取数值 直接整形拿来用 
      // 这里会对record直接产生影响 record会多一个叫parcelString的东西 但是不会对数据库产生影响 当刷新界面的时候 就会重新渲染
    }
  )
  const dataSource = record;   // 这个datasource 就是整形以后放到OrderRecord里去显示 并不会对实际的Record产生任何影响

  
  
  return (
    <div className="OrderHome">
    
        <p> OrderHome </p>  
        <div className="CreateOrder">
          <CreateOrder user={props.user} record={record} setRecord={setRecord}/>
        </div>
        <div>
          <OrderRecord user={props.user} record={record} setRecord={setRecord} dataSource={dataSource}/>
        </div>
        
    </div>
  ) 
}
