import React,{useState, useEffect}from "react";
import {Button,notification } from 'antd';
import { Link } from 'react-router-dom';
import _get from "lodash/get";
 
import "./PriceHome.css";


export default function PriceHome(props) {

  const [dataSource,setDataSource] = useState([])    //  某一用户名字下所有包裹 （包括有分配订单和没有分配订单的包裹）

  // const [originTargetKeys, setOriginTargetKeys] = useState([]) // 当前订单下的所有包裹

  const userId = props.user.obj.uid.toString();

  const thisOrderId = props.match.params.id; 

  useEffect(()=>{
    fetch("/api/order/findAll/"+ userId, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   
    .then(data => {
        setDataSource(data)
     })
  },[])

  const thisOrderData = dataSource.filter(item=>item.id == thisOrderId) // 只需要这一个order的信息
  const price = _get(thisOrderData[0],"price")
  const weight = _get(thisOrderData[0],"weight")
  const volumn = _get(thisOrderData[0],"volumn")
  const pathInfo = _get(thisOrderData[0],"pathInfo")

  const PayFee = ()=>{console.log("pay the money")}
 
    return (
      <div>
        <p>亲爱的客户您好 您将要预付的款项为 {price} RMB </p>
        <p>合并包裹后重量为 {weight} 体积为 {volumn} </p> 
        <p> 为您挑选的路线为 {pathInfo} </p>
        <p> 预付的关税为 </p>

        <div className="parcelUpdate">
          <Link to="/Business/OrderHome">
            <Button size="large" style={{ float: 'center', margin: 5 }}>
              暂不付款
            </Button>
          </Link>
          <Button id="submitOrder" size="large" style={{ float: 'center', margin: 5}} onClick={PayFee}> 
            付款
          </Button>
         
        </div>
      </div>
    );
}
