import React from "react";
import { Route } from "react-router-dom";
import "./SDashBoard.css";
import STopBar from "../STopBar/STopBar";
import OrderHome from "../Order/OrderHome/OrderHome";
import TBHome from "../TB/TBHome/TBHome";
import HistoryHome from "../History/HistoryHome/HistoryHome";
import AddParcelToOrder from "../Order/AddParcelToOrder/AddParcelToOrder";
import UpdateOrderInfo from "../Order/UpdateOrderInfo/UpdateOrderInfo";
//import Commit from "../HistoryHome/Commit/Commit";
import PriceHome from "../Order/PriceHome/PriceHome";
import TrackingHome from "../Order/TrackingHome/TrackingHome";


export default function SDashBoard(props) {

    console.log("SDashBoard")

  return (

    <div className="SDashBoard">

        <STopBar user={props.user} />

        <Route
            exact path="/Business/OrderHome"
            render={internalProps => (
                <OrderHome
                    {...internalProps}
                    user={props.user}
                    isAuthed={true}
                />
            )}
        /> 

        <Route
        exact path="/Business/OrderHome/AddParcel/:id"
        render={internalProps => (
          <AddParcelToOrder
            {...internalProps}
            user={props.user}
            isAuthed={true}
          />
          )}
          /> 

        <Route
        exact path="/Business/OrderHome/OrderInfo/:id"
        render={internalProps => (
          <UpdateOrderInfo
            {...internalProps}
            user={props.user}
            isAuthed={true}
          />
          )}
          /> 

        <Route
        exact path="/Business/OrderHome/PriceInfo/:id"
        render={internalProps => (
          <PriceHome
            {...internalProps}
            user={props.user}
            isAuthed={true}
          />
          )}
          /> 

        <Route
        exact path="/Business/OrderHome/TrackingInfo/:id"
        render={internalProps => (
          <TrackingHome
            {...internalProps}
            user={props.user}
            isAuthed={true}
          />
          )}
          /> 
       <Route
            path="/Business/TBHome"
            render={internalProps => (
                <TBHome
                    {...internalProps}
                    user={props.user}
                    isAuthed={true}
                />
            )}
        /> 
        <Route
            path="/Business/History"
            render={internalProps => (
                <HistoryHome
                    {...internalProps}
                    user={props.user}
                    isAuthed={true}
                />
            )}
        /> 
            

    </div>

  );
}

