import React,{useState, useEffect} from "react";
import { Link } from 'react-router-dom'; 
import _get from "lodash/get";
import {Form, Input, AutoComplete,Button,notification} from "antd";
import "./UpdateOrderForm.css";
 

// 非常好 useEffect里  [] 只刷新一次 但是【】里面写上层传递来的变量 本component就会根据变量的变化来判断是否刷新；
//https://xbuba.com/questions/53070970

 
// user={props.user} thisOrderId={thisOrderId} dataSource={dataSource}


export default function SubmitOrder(props) {

  const FormItem = Form.Item;

  const [form] = Form.useForm();


  const InitialState = ()=>{if(props.thisOrderStatus === 1){return false}else{return true}}  // 一旦提交状态变成1 就不可以进行操作更改了


  useEffect(()=>{form.setFieldsValue({DName:DefaultName,DAddress: DefaultAddress,DPostCode:DefaultPostCode, DPhone: DefaultPhone,DDescription: DefaultDescription })})

  useEffect(()=>{setDisabled(InitialState())},[props.thisOrderStatus])

/***  提交完成后的提醒框 ** */
  const openNotification = () => {
      notification['success']({
        message: 'Successfully update user information',
        description:
          'You have successfully unpdate your personnal information',
        })
    };

/**********   input框 Defaultvalue ********/

 const [disabled,setDisabled]= useState(false)   // 一开始假设都是可以更改 但是后面二次判断是否然后上一层setState以后刷新下层 有值了

// 后面赋予的默认值内容

  const DefaultName = _get(props.thisOrderData,"receiverName");
  const DefaultAddress= _get(props.thisOrderData,"receiverAddress");
  const DefaultPostCode = _get(props.thisOrderData,"receiverPostCode");
  const DefaultPhone = _get(props.thisOrderData,"receiverPhone");
  const DefaultDescription = _get(props.thisOrderData,"description");


/********   搜索框自动填充 自动搜索用户所有订单使用过的地址等信息 然后unique成为唯一list 供他选择********* */

// 填充内容
  function unique(arr){  
    var tmp = new Array();
    for(var i in arr){
        if(tmp.indexOf(arr[i])==-1){
            tmp.push(arr[i]);
        }
    }
    return tmp;
  }

  const listOfReceiver = props.dataSource.map((item,index)=>{ return item.receiverName});
  const listOfAddress = props.dataSource.map((item,index)=>{ return item.receiverAddress});
  const listOfPostCode = props.dataSource.map((item,index)=>{ return item.receiverPostCode});
  const listOfPhone = props.dataSource.map((item,index)=>{return item.receiverPhone});

  const uniqueR = unique(listOfReceiver);
  const uniqueA = unique(listOfAddress);
  const uniquePC = unique(listOfPostCode) ;
  const uniquePh = unique(listOfPhone);

  console.log("unique")
  console.log(uniqueR)

  // 填充框的样式
  const renderTitle = title => (
    <span>
      {title}
    </span>
  );

  const renderItem = (title) => ({
    value: title,
    label: (
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {title}
      </div>
    ),
  });

  const options_name = [
  {
  label: renderTitle('收件人'),
  options: uniqueR.map((item,index)=>renderItem(item))
  }]
  const options_address =[
  {
  label: renderTitle('收件地址'),
  options: uniqueA.map((item,index)=>renderItem(item))
  }]
  const options_postCode =[
  {
  label: renderTitle('邮编'),
  options: uniquePC.map((item,index)=>renderItem(item))
  }]
  const options_phone =[
  {
  label: renderTitle('号码'),
  options: uniquePh.map((item,index)=>renderItem(item))
  }]

/********** *     表单 提交数据   **********/
  const handleSubmit = (values)=>{
    
    const newOrderInfo =
      {
        id:props.thisOrderId,
        userId: props.user.obj.uid,
        statusId: 1, // 提交订单 状态信息等待核实
        price: null , //thisOrderData.price,   都一样 因为此时还不存在值
        description: values.DDescription,
        receiverName: values.DName,
        receiverPhone: values.DPhone,
        receiverAddress: values.DAddress,
        receiverPostCode: values.DPostCode,
        orderId: props.thisOrderData.orderId,
        trackNumber: null,
        senderName: props.thisOrderData.senderName,
        senderAddress: props.thisOrderData.senderAddress,
        senderPhone: props.thisOrderData.senderAddress,
        senderPostCode: props.thisOrderData.senderPostCode,
        orderComment: null,   // 是Admin的comment
        pathInfo: null,
        weight: null,
        volumn: null,
        expectDeliveryDate: null,
        paymentInfo: null,
        deleted: false,
      }

    fetch("/api/order/update", { 
      method: "PUT",
      headers: {
      "Content-Type": "application/json"
      },
      body: JSON.stringify(newOrderInfo),
      })
      .then(resp => resp.json())
      .then(setDisabled(true))
      .then(openNotification())
  }

  return (
   <div className="UpdateOrderForm">
        <div><p>尊敬的{props.user.obj.username}你好 以下是您当前订单信息 修改后请点击保存</p></div>
     <Form 
     form={form}
     onFinish={handleSubmit}
     //onValuesChange = {handleValuesChange}
     className="login-form"
     //initialValues={InitialDValue} 因为是变量做参数 所以使用 form.setFieldsValue
     >
        <div ><p className ="LabelStyle">收件人名</p></div>
        <FormItem  name ="DName" >
          <AutoComplete
            dropdownClassName="certain-category-search-dropdown"
            dropdownMatchSelectWidth={300}
            style={{
              width: 300,
            }}
            options={options_name}
            disabled = {disabled}
            >
            <Input.Search />   
          </AutoComplete>   
      </FormItem>
      <div ><p className ="LabelStyle">收件地址</p></div>
      <FormItem  name ="DAddress">
        <AutoComplete
          dropdownClassName="certain-category-search-dropdown"
          dropdownMatchSelectWidth={300}
          style={{
            width: 300,
          }}
         options={options_address}
          disabled={disabled}
        >
          <Input.Search />   
        </AutoComplete>
      </FormItem>

      <div ><p className ="LabelStyle">收件邮编</p></div>
      <FormItem name = "DPostCode">
        <AutoComplete
          dropdownClassName="certain-category-search-dropdown"
          dropdownMatchSelectWidth={300}
          style={{
            width: 300,
          }}
          options={options_postCode}
          disabled={disabled}
        >
          <Input.Search  />   
        </AutoComplete>
      </FormItem>

      <div ><p className ="LabelStyle">收件号码</p></div>
      <FormItem name= "DPhone">
      <AutoComplete
          dropdownClassName="certain-category-search-dropdown"
          dropdownMatchSelectWidth={300}
          style={{
            width: 300,
          }}
          options={options_phone}
          disabled={disabled}
        >
          <Input.Search />  
        </AutoComplete>
        
      </FormItem>

      <div ><p className ="LabelStyle">备注信息</p></div>
      <FormItem name= "DDescription">
      <AutoComplete
          dropdownClassName="certain-category-search-dropdown"
          dropdownMatchSelectWidth={300}
          style={{
            width: 300,
          }}
          //options={options_phone}
          disabled={disabled}
        >
          <Input.Search />  
        </AutoComplete>
        
      </FormItem>

      <FormItem>
        <Button type="primary" htmlType="submit"  >  
          保存
        </Button> 

        <Link to="/Business/OrderHome">
        <Button type="primary">  
          返回
        </Button> 
        </Link>
    
      </FormItem>

     </Form>
   </div> 
  );
}
