import React,{useState, useEffect,useRef}from "react";
import {Tag,Table,Input,Space,Button, } from 'antd';
import {SearchOutlined} from '@ant-design/icons';
import Highlighter from 'react-highlight-words';
import _get from "lodash/get";
import "./AUserInfoDetail.css";


export default function AUserInfoDetail(props) {

    const [orderData,setOrderData] = useState([])    //  某一用户名字下所有包裹 （包括有分配订单和没有分配订单的包裹）
    const [parcelData,setParcelData] = useState([]) //  

    //const userId = props.user.obj.uid.toString();

    const thisUserId = props.match.params.id; 

    useEffect(()=>{
        fetch("/api/order/findAll/"+ thisUserId, { 
        method: "GET",
        headers: {
            "Content-Type": "application/json"
        },
        })
        .then(resp => resp.json())   
        .then(data => {
            setOrderData(data)
            console.log(data)
        })

        fetch("/api/parcel/findAll/userid/"+ thisUserId, { 
            method: "GET",
            headers: {
                "Content-Type": "application/json"
            },
            })
            .then(resp => resp.json())   
            .then(data => {
                setParcelData(data)
            })
        },[]);

    /*所有的搜索部分需要的状态  */

    const [searchText, setSearchText] = useState("");
    const [searchedColumn, setSearchedColumn] = useState("");
    var searchInput = useRef(null);  //const searchInputReference = {};  searchInputReference.searchInput 直接拿到 这是蔡指导的方法
   
  
    const getColumnSearchProps = dataIndex => ({
      filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
        <div style={{ padding: 8 }}>
          <Input
            ref={node => {
              searchInput = node;
            }}
            placeholder={`Search ${dataIndex}`}
            value={selectedKeys[0]}
            onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
            onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
            style={{ width: 188, marginBottom: 8, display: 'block' }}
          />
          <Space>
            <Button
              type="primary"
              onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
              icon={<SearchOutlined />}
              size="small"
              style={{ width: 90 }}
            >
              Search
            </Button>
            <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
              Reset
            </Button>
          </Space>
        </div>
      ),
      filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
      onFilter: (value, record) =>
        record[dataIndex].toString().toLowerCase().includes(value.toLowerCase()),
      onFilterDropdownVisibleChange: visible => {
        if (visible) {
          setTimeout(() => searchInput.select());   
        }
      },
      render: text =>
        searchedColumn === dataIndex ? (
          <Highlighter
            highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
            searchWords={[searchText]}
            autoEscape
            textToHighlight={text.toString()}
          />
        ) : (
          text
        ),
    });
  
    const handleSearch = (selectedKeys, confirm, dataIndex) => {
      confirm();
  
      setSearchText(selectedKeys[0])
      setSearchedColumn(dataIndex)
    };
  
    const handleReset = clearFilters => {
      clearFilters();
      setSearchText("")
    };

   /* ********** */

    const orderColumns = [
            {
            title: "订单号",
            dataIndex: "id",
            key: "id",
            sorter: (a, b) => a.id - b.id,
            sortDirections: ['descend', 'ascend'],
            ...getColumnSearchProps('id'),
            render: text => <div>{text}</div>,
            },
            {
                title: '订单状态',
                key: 'statusId',
                dataIndex: 'statusId',
                filters: [
                  {
                    text: '未提交',
                    value: "1",
                  },
                  {
                    text: '待处理',
                    value: "2",
                  },
                  {
                    text: '已接受',
                    value: "3",
                  },
                  {
                    text: '关闭',
                    value: "4",
                  },
                  {
                    text: '正在处理',
                    value: "5",
                  },
                  {
                    text: '已结束',
                    value: "6",
                  }],
                  onFilter: (value, record) => record.statusId.toString().indexOf(value) === 0,
                //...getColumnSearchProps('statusId'),
                render: (text, record, index) => { 
                      let status = ""
                      let color = ""  
                      if (record.statusId === 1){
                        color = "default";
                        status = "未提交"
                      } else if(record.statusId === 2){
                        color = "warning";
                        status = "待处理"
                      } else if(record.statusId === 3){
                        color = 'success';
                        status = "已接受"
                      }
                      else if(record.statusId === 4){
                        color = "error";
                        status = "关闭"
                      }
                      else if(record.statusId === 5){
                        color = "processing";
                        status = "正在处理"
                      }
                      else{
                        color = "default";
                        status = "已结束"
                      }
                      return (
                        <Tag color={color} key={index}>
                          {status}
                        </Tag>
                      );
                    }},
            {
                title: "价格",
                dataIndex:"price",
                key:"price"
            },
            {
                title: "支付信息",
                dataIndex: "paymentInfo",
                key: "paymentInfo"
            },
            {
                title: "追踪单号",
                dataIndex: "trackNumber",
                key: "trackNumber"
            },
            {
                title: "路线信息",
                dataIndex: "pathInfo",
                key: "pathInfo"
            },
            {
                title: "预计到达日期",
                dataIndex: "expectDeliveryDate",
                key: "expectDeliveryDate",
            },
            {
                title: "创建日期",
                dataIndex: "createAt",
                key: "createAt", 
                //textWrap: 'word-break',
                ellipsis: true,
                sorter: (a, b) =>new Date(a.createAt) - new Date(b.createAt),
                sortDirections: ['descend', 'ascend'],
            },
            {
                title: "最新修改日期",
                dataIndex: "modifiedAt",
                key: "modifiedAt",
                ellipsis: true,
                sorter: (a, b) =>new Date(a.modifiedAt) - new Date(b.modifiedAt),
                sortDirections: ['descend', 'ascend'],
            },
            {
                title: "管理员备注",
                dataIndex: "orderComment",
                key: "orderComment",
                ellipsis: true,
                // textWrap: 'word-break', // 可以根据宽度进行折叠 这里是管理员对order写的备注
            }, 
        ]  



    const parcelColums= [
            {
              title: "包裹号",
              dataIndex: "id",
              key: "id",
              sorter: (a, b) => a.id - b.id,
              sortDirections: ['descend', 'ascend'],
              ...getColumnSearchProps('id'),
              render: text => <div>{text}</div>,
            },
            {
              title: "包裹状态",
              dataIndex: "parcelStatus",
              key: "parcelStatus",
              //...getColumnSearchProps('parcelStatus'),
              filters: [
                {
                  text: '等待',
                  value: 'waiting',
                },
                {
                  text: '接受',
                  value: 'verify',
                },
                {
                  text: '问题',
                  value: 'problem',
                }],
                onFilter: (value, record) => record.parcelStatus.indexOf(value) === 0,
                render: (text, record, index) => { 
                    let status = ""
                    let color = ""  
                    if (record.parcelStatus === "waiting"){
                      color = "warning";
                      status = "等待"
                    } else if(record.parcelStatus === "verify"){
                      color = "success";
                      status = "接受"
                    } else if(record.parcelStatus === "problem"){
                      color = 'error';
                      status = "问题"
                    }
                    return (
                      <Tag color={color} key={index}>
                        {status}
                      </Tag>
                    );
                  }
            },
            {
                title: "运单号",
                dataIndex: "orderNumber",
                key: "orderNumber",
                ...getColumnSearchProps('orderNumber'),
                render: text => <div>{text}</div>,
                },

            {
                title: '所属订单',
                key: 'userOrderId',
                dataIndex: 'userOrderId',
                ...getColumnSearchProps('userOrderId'),
                render: text => <div>{text}</div>,
            },
            {
                title: "包裹内容",
                dataIndex: "contentType",
                key: "contentType",
                ...getColumnSearchProps('contentType'),
                render: text => <div>{text}</div>,
            },
            {
                title: "创建日期",
                dataIndex: "createAt",
                key: "createAt",
                ellipsis: true,
                sorter: (a, b) =>new Date(a.createAt) - new Date(b.createAt),
                sortDirections: ['descend', 'ascend'],
            },
            {
                title: "最新修改日期",
                dataIndex: "modifiedAt",
                key: "modifiedAt",
                ellipsis: true,
                sorter: (a, b) =>new Date(a.modifiedAt) - new Date(b.modifiedAt),
                sortDirections: ['descend', 'ascend'],
            },  
            {
              title: "管理员备注",
              dataIndex: "parcelComment",
              key: "parcelComment",
              ellipsis: true,
              // textWrap: 'word-break',//暂时还没值
          }, 
        ]  


    let orderDataSource;
    let parcelDataSource;

  if(orderData){
    orderDataSource = orderData.map(item => {
      return {
        ...item,
        key: item.id
      }
    })
  };


  if(parcelData){
    parcelDataSource = parcelData.map(item => {
      return {
        ...item,
        key: item.id
      }
    })
  }
  
    return (
      <div className="AUserInfoDetailContainer">
        <p>用户所有订单信息</p>  
        <Table 
            dataSource={orderDataSource} columns={orderColumns} 
            expandable={
                {
                  expandedRowRender: (record) => {
                    return(
                    <div>
                      <p>创建日期{record.createAt} &nbsp; 最新修改日期{record.modifiedAt}</p>
                      <p>收件人 &nbsp; {record.receiverName} &nbsp;  {record.receiverPhone} &nbsp; 地址 {record.receiverAddress } &nbsp; {record.receiverPostCode}</p>
                      <p>重量{record.weight} 体积{record.volumn} &nbsp; 订单描述 {record.description}</p>
                      <p>管理员备注 {record.orderComment}</p>   
                    </div>
                    )}
                }
            }/>

        <p>用户所有包裹信息</p>

        <Table 
            dataSource={parcelDataSource} columns={parcelColums}
            expandable={
                {
                  expandedRowRender: (record) => {
                    return(
                    <div>
                      <p>创建日期{record.createAt} &nbsp; 最新修改日期{record.modifiedAt}</p>
                      <p>发件人 &nbsp; {record.senderName} &nbsp;  {record.senderPhone}</p>
                      <p>发件地址 {record.senderAddress } &nbsp; {record.senderPostCode}</p>
                      <p>包裹描述 {record.description}</p>
                      <p>管理员备注 {record.parcelComment}</p>   
                    </div>
                    )}
                }
              } />
      </div>
    );
    
    }


    /* 当parcel被删除后  就不会在fetch中被拿到了 但是仍然存在于数据库当中*/