import React,{useState,useEffect} from "react";
import _get from "lodash/get";
import AOrderForm from "../AOrderForm/AOrderForm";
import AOrderSearch from "../AOrderSearch/AOrderSearch";
import "./AdminOrder.css";

export default function AdminOrder(props) {

  // console.log(props.user.obj.uid)   // number

  //const userId = props.user.obj.uid.toString();

  const [search,setSearch] = useState({userId:"",orderId:"",orderTy:"",searchAll:""}) ;        // userId is the only way to get orderInfo
  
  console.log(search)
  

  const [record,setRecord] = useState([]);  // record has to be a list
  
  useEffect(()=>{
    if(search.userId){
      fetch(`/api/order/findAll/${search.userId}`, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())
      .then(data => {
        setRecord(data); 
       })     
  }},[search.userId]) ;

  useEffect(()=>{
    if(search.orderId){  // 修改路径 添加一个直接根据orderID就能找到订单的方法
      fetch(`/api/order/find/${search.orderId}`, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())
      .then(data => {
        setRecord([data]);    // 必须是arry 才能map  特别注意一下单条数据自己成arry 外面哟啊加[]
      })  
    }},[search.orderId])   // 只要输入的数字在变化 5-》13-》14 判断后就会渲染； 如果没变 15-》15 那么搜索框变了 setSearch 也触发了 但是use Effect不会执行 那么record也就不会reset

    useEffect(()=>{
      if(search.orderTy){  
        fetch(`/api/order/status/${search.orderTy}`, { 
          method: "GET",
          headers: {
            "Content-Type": "application/json"
          },
        })
        .then(resp => resp.json())
        .then(data => {
          setRecord(data);    
        })  
      }},[search.orderTy]) 

      useEffect(()=>{
        if(search.searchAll){ 
          fetch(`/api/order`, { 
            method: "GET",
            headers: {
              "Content-Type": "application/json"
            },
          })
          .then(resp => resp.json())
          .then(data => {
            setRecord(data); 
          })  
        }},[search.searchAll]) 
  
  return (
    <div className="OrderHome">
    
        <p> OrderHome </p>  
        <div className="AOrderSearch">
          <AOrderSearch user={props.user} search={search} setSearch={setSearch}/>
        </div>
        <div>
          <AOrderForm user={props.user} record={record} setRecord={setRecord} />
        </div>
        
    </div>
  ) 
}
