
import React,{useState, useEffect}from "react";
import { Link } from 'react-router-dom'; 
import { DatePicker,AutoComplete,Form,notification,Input,Button} from 'antd';
import _get from "lodash/get";
import "./AOrderUpdate.css";

export default function AOrderUpdate(props) {

  const FormItem = Form.Item;

  const [form] = Form.useForm();

  const thisOrderId = props.match.params.id;


    /*---------------------找到一个办法直接根据parcelID去拿 到parcel信息 这样就可以直接修改了------------------------- */

  const [dataSource,setDataSource] = useState()  



  useEffect(()=>{
    fetch(`/api/order/find/${thisOrderId}`, { 
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
    })
    .then(resp => resp.json())   // 拿到的是dictionary格式 Json ：[{"key":"123"}] 转换为obj 从而进行操作；
    .then(data => {
      setDataSource(data);  //[{}{}{}{}] 因为返回值data就是一个list
     })
    
  },[]) 

  useEffect(()=>{form.setFieldsValue(
                    { DName:DefaultName,DAddress: DefaultAddress,DPostCode:DefaultPostCode, DPhone: DefaultPhone,
                      DDescription: DefaultDescription,DOrderComment:DefaultOrderComment,DPrice:DefaultPrice,
                      DTrackNumber:DefaultTrackNumber,DPathInfo:DefaultPathInfo,DWeight:DefaultWeight,
                      DVolumn:DefaultVolumn,DEDD:DefaultEDD,DPaymentInfo:DefaultPaymentInfo,
                    }
                  )
                }
              )

 // useEffect(()=>{setDisabled(InitialState())},[]) //dataSource.statusId  管理员有权限任意修改 不需要判断初始情况

/***  提交完成后的提醒框 ** */
  const openNotification = (resp) => {
      notification['success']({
        message: 'Successfully update user information',
        description:
          `${resp.trackNumber} You have successfully unpdate your personnal information`,
        })
    };

/**--------- 线路 pathInfo 填充框的样式-----------*** */
  const renderTitle = title => (
    <span>
      {title}
    </span>
  );

  const renderItem = (title) => ({
    value: title,
    label: (
      <div
        style={{
          display: 'flex',
          justifyContent: 'space-between',
        }}
      >
        {title}
      </div>
    ),
  });

  const path =["路线1 特快","路线2 平邮","路线3 e邮宝","路线4 EMS","路线5 燕文小包"] 

  const options_path = [
  {
  label: renderTitle('线路选择'),
  options: path.map((item,index)=>renderItem(item))
  }]
  

/**********   input框 Defaultvalue ********/


// 后面赋予的默认值内容

  const DefaultName = _get(dataSource,"receiverName");
  const DefaultAddress= _get(dataSource,"receiverAddress");
  const DefaultPostCode = _get(dataSource,"receiverPostCode");
  const DefaultPhone = _get(dataSource,"receiverPhone");
  const DefaultDescription = _get(dataSource,"description");
  const DefaultOrderComment = _get(dataSource,"orderComment");
  const DefaultPrice = _get(dataSource,"price");
  const DefaultTrackNumber = _get(dataSource,"trackNumber");
  const DefaultPathInfo = _get(dataSource,"pathInfo");
  const DefaultWeight = _get(dataSource,"weight");
  const DefaultVolumn = _get(dataSource,"volumn");
  const DefaultEDD = _get(dataSource,"expectDeliveryDate");
  const DefaultPaymentInfo = _get(dataSource,"paymentInfo");


/********** *     表单 提交数据   **********/
  const handleSubmit = (values)=>{
    
    const newOrderInfo =
      {
        id:thisOrderId,
        userId: dataSource.userId,
        statusId: dataSource.statusId, // 提交订单 状态信息等待核实
        price: values.DPrice , //thisOrderData.price,   都一样 因为此时还不存在值
        description: values.DDescription,
        receiverName: values.DName,
        receiverPhone: values.DPhone,
        receiverAddress: values.DAddress,
        receiverPostCode: values.DPostCode,
        orderId: dataSource.orderId,
        trackNumber: values.DTrackNumber,
        senderName: dataSource.senderName,
        senderAddress: dataSource.senderAddress,
        senderPhone: dataSource.senderPhone,
        senderPostCode: dataSource.senderPostCode,
        orderComment: values.DOrderComment,   // 是Admin的comment
        pathInfo: values.DPathInfo,
        weight: values.DWeight,
        volumn: values.DVolumn,
        expectDeliveryDate: values.DEDD,
        paymentInfo: values.DPaymentInfo,
        deleted: dataSource.deleted,
      }
    

    fetch("/api/order/update", { 
      method: "PUT",
      headers: {
      "Content-Type": "application/json"
      },
      body: JSON.stringify(newOrderInfo),
      })
      .then(resp => resp.json())
      .then(resp=>{
        console.log(resp)
        openNotification(resp)})
     
  }

  return (
   <div className="UpdateOrderForm">
        <div><p>管理员你好 以下是订单{thisOrderId}的信息 修改后请点击保存</p></div>
     <Form 
     form={form}
     onFinish={handleSubmit}
     className="orderUpdate-form"
     //initialValues={InitialDValue} 因为是变量做参数 所以使用 form.setFieldsValue
     >
        <div ><p className ="LabelStyle">收件人名</p></div>
        <FormItem  name ="DName" >
          <Input/>
        </FormItem>
        <div ><p className ="LabelStyle">收件地址</p></div>
        <FormItem  name ="DAddress">
          <Input/>
        </FormItem>

        <div ><p className ="LabelStyle">收件邮编</p></div>
        <FormItem name = "DPostCode">
          <Input/>
        </FormItem>

        <div ><p className ="LabelStyle">收件号码</p></div>
        <FormItem name= "DPhone">
          <Input/>
        </FormItem>

        <div ><p className ="LabelStyle">客户方备注信息</p></div>
        <FormItem name= "DDescription">
        <Input/> 
        </FormItem>  

        <div ><p className ="LabelStyle">管理方备注信息</p></div>
        <FormItem name= "DOrderComment">
        <Input/> 
        </FormItem>

        <div ><p className ="LabelStyle">运费</p></div>
        <FormItem name= "DPrice">
        <Input/> 
        </FormItem>

        <div ><p className ="LabelStyle">支付信息</p></div>
        <FormItem name= "DPaymentInfo">
        <Input/> 
        </FormItem>

        <div ><p className ="LabelStyle">线路</p></div>
        <FormItem name= "DPathInfo">
          <AutoComplete
            dropdownClassName="certain-category-search-dropdown"
            dropdownMatchSelectWidth={300}
            style={{
              width: 300,
            }}
          options={options_path}
          >
            <Input.Search />   
          </AutoComplete>
        </FormItem>

        <div ><p className ="LabelStyle">重量</p></div>
        <FormItem name= "DWeight">
        <Input/> 
        </FormItem>

        <div ><p className ="LabelStyle">体积</p></div>
        <FormItem name= "DVolumn">
        <Input/> 
        </FormItem>

        <div ><p className ="LabelStyle">订单跟踪号</p></div>
        <FormItem name= "DTrackNumber">
        <Input/> 
        </FormItem>


        <FormItem>
          <Button type="primary" htmlType="submit" >  
            保存
          </Button> 

          <Link to="/Admin/Order">
          <Button type="primary">  
            返回
          </Button> 
          </Link>
    
      </FormItem>

     </Form>
   </div> 
  );
  
}