import React,{useState,useEffect}from 'react';
import 'antd/dist/antd.css';
import AParcelSearch from "../AParcelSearch/AParcelSearch";
import AParcelForm from "../AParcelForm/AParcelForm";
import "./AdminParcel.css"

export default function AdminParcel(props){

  console.log("AdminParcel参数传递")
  // console.log(props.user.obj.uid)   // number

  //const userId = props.user.obj.uid.toString();

  const [search,setSearch] = useState({searchRg:"",searchTy:"",userId:"",username:"",orderId:""}) ;        // userId is the only way to get orderInfo

  const [record,setRecord] = useState([]);

  console.log("search的值是多少")
  console.log(search)

  console.log(record)
  
  useEffect(()=>{
    if(search.userId){
      fetch(`/api/parcel/findAll/userid/${search.userId}`, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())
      .then(data => {
        setRecord(data);  
       })     
  }},[search.userId]) ;

  useEffect(()=>{
    if(search.username){
      fetch(`/api/parcel/findAll/username/${search.username}`, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())
      .then(data => {
        setRecord(data);  
       })     
  }
  },[search.username]) ;

  useEffect(()=>{
    if(search.orderId){  // 修改路径 添加一个直接根据orderID就能找到订单的方法
      fetch(`/api/parcel/findAll/${search.orderId}`, { 
        method: "GET",
        headers: {
          "Content-Type": "application/json"
        },
      })
      .then(resp => resp.json())
      .then(data => {
        setRecord(data);  
      })  
    }},[search.orderId]);  // 只要输入的数字在变化 5-》13-》14 判断后就会渲染； 如果没变 15-》15 那么搜索框变了 setSearch 也触发了 但是use Effect不会执行 那么record也就不会reset

    useEffect(()=>{
      if(search.searchRg === "searchAll"){  // 修改路径 添加一个直接根据orderID就能找到订单的方法
        fetch(`/api/parcel`, { 
          method: "GET",
          headers: {
            "Content-Type": "application/json"
          },
        })
        .then(resp => resp.json())
        .then(data => {
          setRecord(data);  
        })  
      }},[search.searchRg]) 

      useEffect(()=>{
        console.log(`/api/parcel/status/${search.searchTy}`)
        if(search.searchTy){  // 修改路径 添加一个直接根据orderID就能找到订单的方法
          fetch(`/api/parcel/status/${search.searchTy}`, { 
            method: "GET",
            headers: {
              "Content-Type": "application/json"
            },
          })
          .then(resp => resp.json())
          .then(data => {
            setRecord(data);  
            
          })  
        }},[search.searchTy]) 
  
  return (
    <div className="OrderHome">
    
        <p> OrderHome </p>  
        <div className="AParcelSearch">
          <AParcelSearch user={props.user} search={search} setSearch={setSearch}/>
        </div>
        <div>
          <AParcelForm  user={props.user} record={record} setRecord={setRecord} />
        </div>
        
    </div>
  ) 
  
}
