import React from "react";
import { Card, Avatar,Col, Row,Divider,Tag,Popover  } from 'antd';
import _get from "lodash/get";
import { CheckCircleOutlined,InboxOutlined,ApiOutlined,SkinOutlined,
  ClockCircleOutlined,MailOutlined,ExperimentOutlined,CoffeeOutlined,ExclamationCircleOutlined} from '@ant-design/icons';
 
import "./TBCard.css";
// import IconButton from '@material-ui/core/IconButton';


export default function TBcard(props) {

  // const test =()=>{console.log("hello")}

  const { Meta } = Card;

  // dataSource = props.record.map(x=>x["content"])

  const dataSource = props.record;

  console.log("dataSource")
  console.log(dataSource)
  
  /*  自动补齐单号15位数保障卡片宽度一致
  const supplyFunc = (str, len) => {
    if(str.length > len) return str.substr(0, len);   // 只显示15位
    if(str.length < len) return supplyFunc("\t"+ str , len);  // 添加2位 再循环 一直加满到1位为止 //"&nbsp" 不成功；“/t” 成功增加字符长度 但是没有显示
    
    return str;  // 等于15位
  }
 */

// 要么直接设置card宽度 以15位为准

//options: [renderItem("普货"),renderItem("文件"),renderItem("电子产品"),renderItem("化妆品"),renderItem("食物"),renderItem("其他")],

  const AvaColor=(prop)=>{
    if(prop==="普货"){return "#87e8de"}
    else if(prop==="文件"){return  "#613400"} 
    else if(prop==="电子产品"){return  "#ffec3d"}
    else if(prop==="食品"){return  "#b7eb8f"} 
    else if(prop==="化妆品"){return  "#ff85c0"}
    else {return "#8c8c8c"}}

  const Icon = (prop)=>{ 
    if(prop==="普货"){return <SkinOutlined />}
    else if(prop==="文件"){return  <MailOutlined />}
    else if(prop==="电子产品"){return <ApiOutlined />} 
    else if(prop==="食品"){return  <CoffeeOutlined />}
    else if(prop==="化妆品"){return  <ExperimentOutlined />}
    else {return <InboxOutlined/>}}

  const TagType = (prop)=>{
    if(prop.parcelStatus ==="waiting"){return (
      <Tag icon={<ClockCircleOutlined />} color="default">   
      Waiting
      </Tag>)}
    else if(prop.parcelStatus ==="verify"){return (
      <Tag icon={<CheckCircleOutlined />} color="success">
      Verified
      </Tag>)}
    else {return (
      <Tag icon={<ExclamationCircleOutlined />} color="error">
      Problem
      </Tag>)}
    }
  


  const cardStyle = {
    width: "80%"
  }
  return (
    <Row gutter={24}>
      { dataSource.map((item, index) => {
        const orderNumber= _get(item, "orderNumber");
        const orderInfo = orderNumber.split(" ")  //orderNumber.split(" ")  // 使用空格衔接快递公司和编号存储 所以此时可以用split分开
        const company = orderInfo[0]  // 公司名
        const number = orderInfo[1]   // 单号名

        const contentType=_get(item,"contentType")
        // const TBInfo = _get(item, "TBNumber");
        // const TBInfo = supplyFunc(TBInfo_origin,15)   // 自动补齐15位
        //console.log(TBInfo.length)
        const description = _get(item, "description")
        const popInfo = (<p>{description}</p>)
        const parcelStatus = _get(item, "parcelStatus");
      
      return (
        <Col span={6} >
          <Popover content ={popInfo} title="content" placement="right" trigger="click">
            <Card className="Card_Container"
              key={index} hoverable style={cardStyle}
            
                 // 尝试使用styled.component去改写 根据不同浏览器去写入CSS ； element.style 不知道如何单独改写 // 否则只能填充
            /*
              actions={[
                <SettingOutlined onClick = {test} disabled key="setting"/>,
                <CheckCircleOutlined onClick = {test} key="Adding" /> //disabled={AddInfo}
              ]}
            */
              
            >
              <Meta
                avatar={<Avatar style={{
                    backgroundColor: AvaColor(contentType),
                    verticalAlign: 'middle',
                  }} icon={Icon(contentType)}  size="large" />} // src = "" 可以放入图片

                title={company} //   title={orderInfo[0]} 
                description={number}
              />
              <Divider style={{marginLeft: "0px"}}></Divider>
            
              <TagType id="tagType" parcelStatus={parcelStatus}/>     

            </Card>
          </Popover> 
        </Col>
      );
      })}
   </Row>
  )
}
